"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Utils = require("../utils");
class Font {
    constructor(data) {
        this.family = 'font';
        this.size = 0;
        this.color = 0xffffff;
        this.lineHeight = 0;
        this.family = Utils.getString(data, 'family', this.family);
        // @todo 以后进行修改 先临时指定为font关键字
        this.family = 'font';
        this.size = Utils.getInt(data, 'size', this.size);
        let color = Utils.getString(data, 'color', '#ffffff');
        this.color = parseInt(color.replace('#', '0x'));
        this.lineHeight = Utils.getInt(data, 'lineHeight', this.lineHeight);
    }
}
exports.default = Font;



