// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Image from './image';
import Text from './text';

import ElementButton from '../element/button';
import ElementOption from '../element/option';

export default class Button extends Phaser.Group {
    /**
     * @type {Model.Material.Metainfo.Button}
     */
    model;

    /**
     * @type {ElementButton | ElementOption}
     */
    parent;

    /**
     *
     * @param {Phaser.Game} game
     * @param {ElementButton | ElementOption} parent
     * @param {Model.Material.Metainfo.Button} model
     * @parma {{name:string, size:number, color:number}} font
     */
    constructor(game, parent, model, font) {
        super(game, parent, 'MaterialButton' + parent.model.materialId);
        this.init(model, font);
    }

    /**
     *
     * @param {Model.Material.Metainfo.Button} model
     * @parma {{name:string, size:number, color:number}} font
     */
    init(model, font) {

        /**
         * @type {Model.Material.Metainfo.Button}
         */
        let metainfo = model;

        this.x = metainfo.x;
        this.y = metainfo.y;
        this.width = metainfo.w;
        this.height = metainfo.h;

        // 需要打开子元素的输入事件
        this.inputEnableChildren = true;

        this.normalImage = new Image(this.game, this, metainfo.subs.normal);
        this.hoverImage = new Image(this.game, this, metainfo.subs.hover);
        this.hoverImage.visible = false;

        // let graphics = this.game.add.graphics(metainfo.x, metainfo.y);
        // graphics.lineStyle(2, 0xffd900, 1);
        // graphics.drawRect(0, 0, metainfo.w, metainfo.h);
        // this.add(graphics);

        let textData = metainfo.subs.text;
        if (textData.text) {
            let renderText = new Text(this.game, this, textData, font);

            // 需要计算下文本的宽度和高度
            renderText.updateTransform();

            // 重新定位居中
            renderText.position.x = (textData.w - renderText.textWidth) / 2;
            renderText.position.y = (textData.h - renderText.textHeight) / 2;
        }
    }

    bindInteraction() {
        this.onChildInputOver.add(this.onChildInputOverHandler, this);
        this.onChildInputOut.add(this.onChildInputOutHandler, this);
        this.onChildInputDown.add(this.onChildInputDownHandler, this);
    }

    unbindInteraction() {
        this.onChildInputOver.remove(this.onChildInputOverHandler, this);
        this.onChildInputOut.remove(this.onChildInputOutHandler, this);
        this.onChildInputDown.remove(this.onChildInputDownHandler, this);
    }

    onChildInputOverHandler(sprite, pointer) {
        this.normalImage.visible = false;
        this.hoverImage.visible = true;
    }

    onChildInputOutHandler(sprite, pointer) {
        this.normalImage.visible = true;
        this.hoverImage.visible = false;
    }

    onChildInputDownHandler(sprite, pointer) {
        if (this.parent.model.interaction) {

            // 元素回到初始状态
            this.normalImage.visible = true;
            this.hoverImage.visible = false;

            Interactor.trigger(this.parent.model.interaction);
        }
    }

    destroy(destroyChildren, soft) {
        this.unbindInteraction();

        this.normalImage = null;
        this.hoverImage = null;

        super.destroy(true, soft);
    }
}





