// @ts-check
import * as _ from 'lodash';
import { Signal } from 'signals';

import { Phaser } from '../../utils/load-phaser';
import Element from '../element/base';

class EventCounter {
    count = 0;

    start() {
        this.count += 1;
    }

    complate() {
        this.count -= 1;
    }

    isFinish() {
        return this.count <= 0;
    }
}

export default class Base extends Phaser.State {
    /**
     * @type {Phaser.Group}
     */
    baseGroup;

    /**
     * @type {EventCounter}
     */
    event;

    onRenderFinish = new Signal();

    init() {
        this.baseGroup = this.game.add.group();
        // @ts-ignore 这句提示有问题，暂时没有想到具体解决方案
        this.baseGroup.parent.sendToBack(this.baseGroup);

        this.event = new EventCounter();
    }

    captureScreen() {
        var game = this.game;
        var screen = game.make.bitmapData(game.width, game.height);
        screen.copy(game.canvas, 0, 0, game.width, game.height, 0, 0, game.width, game.height);
        return screen;

    }

    findElement(id) {
        /**
         * @type {Element}
         */
        let element;
        // @ts-ignore 这里是因为 children 的类型写的有问题，无法正常提示
        element =  _.find(this.baseGroup.children, (child) => {
            let model = _.get(child, 'model');
            return model && model.id === id;
        });
        return element;
    }

    shutdown() {
        if (this.baseGroup) {
            this.baseGroup.destroy(true, true);
            this.baseGroup = null;
        }

        this.event = null;
    }
}



