import {Phaser, PIXI} from './load-phaser';

Phaser.Plugin.MobileScreenRotation = function (game, parent) {
    'use strict';
    Phaser.Plugin.call(this, game, parent);
    this.width = this.game.width;
    this.height = this.game.height;
    this.screenOrientation = Phaser.DOM.getScreenOrientation();
};

let MSR = Phaser.Plugin.MobileScreenRotation;
MSR.prototype = Object.create(Phaser.Plugin.prototype);
MSR.constructor = MSR;

MSR.prototype.init = function() {
    var _this = this;

    var game = this.game;

    // scale 参数重置
    game.scale.trackParentInterval = 20;
    game.scale.scaleMode = Phaser.ScaleManager.SHOW_ALL;
    game.scale.compatibility.forceMinimumDocumentHeight = true;
    game.scale.pageAlignHorizontally = true;
    game.scale.pageAlignVertically = true;

    game.scale.windowConstraints.right = 'visual';
    game.scale.windowConstraints.bottom = 'visual';

    // 移除掉 Phaser 的 orientationchange
    window.removeEventListener('orientationchange', game.scale._orientationChange, false);

    // 替换成插件的 orientationchange
    this._orientationChange = function(event) {
        _this.orientationChange(event);
    };
    window.addEventListener('orientationchange', this._orientationChange, false);

    this.fitScreen();

    this.x = this.game.world.x;

    // 基于 world 处理屏幕旋转
    this.game.world.displayObjectUpdateTransform = function() {

        if (this._cacheAsBitmap) {
            return;
        }

        if (_this.isRotated()) {
            // this.x = this.game.width;
            this.x = this.game.width;
            this.rotation = Phaser.Math.degToRad(Phaser.Math.wrapAngle(90));
        } else {
            this.x = 0;
            this.rotation = 0;
        }

        PIXI.DisplayObject.prototype.updateTransform.call(this);
    };
};

MSR.prototype.orientationChange = function(event) {
    this.fitScreen();
    this.game.scale._orientationChange(event);
};

MSR.prototype.fitScreen = function() {
    var game = this.game;
    this.screenOrientation = Phaser.DOM.getScreenOrientation();

    var width = this.width;
    var height = this.height;

    if (this.isRotated()) {
        game.canvas.parentElement.style.height = Phaser.DOM.visualBounds.right + 'px';
        width = this.height;
        height = this.width;
    } else {
        game.canvas.parentElement.style.height = Phaser.DOM.visualBounds.bottom + 'px';
        width = this.width;
        height = this.height;
    }

    game.width = width;
    game.height = height;
    game.scale.updateDimensions(width, height, true);
    game.stage.updateTransform();
};

MSR.prototype.isRotated = function() {
    return this.game.scale.classifyOrientation(this.screenOrientation) === 'landscape';
};

export default Phaser.Plugin.MobileScreenRotation;



