import * as Model from '../../model';
import Interactor from '../../core/interactor';

import Item from './item';

export default class List {
    /**
     * @type {Item[]}
     */
    items = [];
     /**
     * @type {Item}
     */
    current = null;

    reset() {
        if (this.current) {
            this.popdownAll();
        }
        this.items = [];
        this.current = null;
    }

    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Scene.Item} ui
     */
    popup(game, parent, ui) {
        Interactor.popup();

        if (this.current) {
            this.items.push(this.current);
        }

        this.current = new Item(game, parent, ui);
    }

    popdown() {

        Interactor.popdown();

        if (this.current) {
            // @以后可以缓存下实际的ui内容
            this.current.parent.remove(this.current, true, true);
            this.current = this.items.pop();
        }
    }

    popdownAll() {
        do {
            this.popdown();
        } while(this.items.length > 0 || this.current);
    }

    existUI() {
        return this.items.length > 0;
    }

    getCurrent() {
        return this.current;
    }
}



