// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';

import * as Element from '../element';

export default class Item extends Phaser.Group {

    model;

    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Scene.Item.Sketch} model
     */
    constructor(game, parent, model) {
        super(game, parent, model.id.toString());
        this.model = model;
        this.init();
    }

    init() {
        if (this.model.hasElement()) {
            for(let i = 0; i < this.model.element.list.length; i++) {
                let element = this.model.element.list[i];
                Element.factory.create(this.game, this, element);
            }
        }
    }

    destroy(destroyChildren, soft) {
        this.model = null;

        super.destroy(true, soft);
    }
}



