"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Collection = require("../../base/collection");
const Utils = require("../../utils");
class Item extends Collection.Item {
    constructor(data) {
        super();
        this.id = -1;
        this.desc = '';
        // 当前值
        this.value = 0;
        // 原始值
        this.originValue = 0;
        // 存档值
        this.savedValue = 0;
        this.id = Utils.getInt(data, 'id', this.id);
        this.value = Utils.getInt(data, 'value', this.value);
        this.savedValue = this.originValue = this.value;
        this.desc = Utils.getString(data, 'desc', this.desc);
    }
    reset() {
        this.savedValue = this.value = this.originValue;
    }
    get() {
        return this.value;
    }
    set(value) {
        this.value = value;
    }
    check(operator, value) {
        var result = false;
        switch (operator) {
            case "=":
                result = this.value == value;
                break;
            case "<":
                result = this.value < value;
                break;
            case "<=":
                result = this.value <= value;
                break;
            case ">":
                result = this.value > value;
                break;
            case ">=":
                result = this.value >= value;
                break;
            case "!=":
                result = this.value != value;
                break;
        }
        return result;
    }
    toSaved() {
        return this.savedValue;
    }
    fromSaved(savedValue) {
        console.log(`Model Game Variable: 读取存档变化 V${this.id} ${this.value} -> ${savedValue}`);
        this.savedValue = this.value = savedValue;
    }
    changeSaved() {
        this.savedValue = this.value;
    }
}
exports.default = Item;



