import {Phaser, PIXI} from './../load-phaser';

import View from './view';
import Transition from './transition';

class StateTransition {

    /**
     *
     * @param {Phaser.Game} game
     */
    constructor(game) {
        this.game = game;
    }

    /**
     *
     * @param {string} stateKey
     * @param {object} transitionData
     */
    start(stateKey, transitionData, clearWorld, clearCache, ...args) {
        let outView = new View(this.game);

        /**
         * @type {Phaser.State}
         */
        let state = this.game.state.states[stateKey];
        let _this = this;
        (function(state, transitionData, outView) {
            let oldCreate = state.create;
            state.create = function() {
                oldCreate.call(state);

                let inView = new View(this.game);
                let transition = Transition.factory.create(this.game, outView, inView, transitionData);

                if (!transition) {
                    return;
                }

                state.game.state._created = false;

                transition.onComplete.addOnce(function() {
                    state.game.state._created = true;

                    // 增加一个执行回调函数
                    if (state.postTransit && typeof state.postTransit === 'function') {
                        state.create = oldCreate;
                        state.postTransit.call(state);
                    }
                });

                transition.run();
            };

        })(state, transitionData, outView);

        this.game.state.start(stateKey, clearWorld, clearCache, ...args);
    }
}

Phaser.Plugin.StateTransition = StateTransition;

export { StateTransition, Transition };



