// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Base from './base';

export default class Rain extends Base {
    emitters = [];
    constructor(game) {
        super(game);
    }

    run() {
        //this.addImage('rain2', this.rain2, this.start.bind(this));
        this.start();
    }

    start() {
        /**
         * @type {Phaser.Game}
         */

        let game = this.game;

        let emitter1 = game.add.emitter(game.width / 2, 0, 1200);
        let emitter2 = game.add.emitter(game.width / 2, game.height / 3, 10);
        let emitter3 = game.add.emitter(game.width / 2, game.height * 2 / 3, 10);
        emitter1.makeParticles('effect',['rain1.png']);
        emitter2.makeParticles('effect',['rain2.png']);
        emitter3.makeParticles('effect',['rain2.png']);

        let emitters = [emitter1, emitter2, emitter3];

        for (var i = 0; i < emitters.length; i++) {
            emitters[i].width = game.width;

            emitters[i].area = new Phaser.Rectangle(0, 0, this.game.width, this.game.height);

            emitters[i].minParticleScale = 0.4;
            emitters[i].maxParticleScale = 0.8;

            emitters[i].setYSpeed(500, 800);
            emitters[i].setXSpeed(-5, 5);

            emitters[i].setAlpha(80, 100);

            emitters[i].minRotation = 0;
            emitters[i].maxRotation = 0;

            emitters[i].start(false, 1000, 5, 0);

        }

        this.emitters = emitters;
        this.timeEvent = game.time.events.add(Phaser.Timer.SECOND * 1, this.destroyRedundancyEmitters, this, this.emitters);

    }
    //将第二、三个发射器销毁，只留第一个
    destroyRedundancyEmitters(emitters) {
        while (emitters.length > 1) {
            emitters[emitters.length - 1].destroy();
            emitters.pop();
        }
    }

    pause() {
        if (this.emitters.length > 0) {
            for (var i = 0; i < this.emitters.length; i++) {
                this.emitters[i].visible = false;
            }
        }
    }

    resume() {
        if (this.emitters.length > 0) {
            for (var i = 0; i < this.emitters.length; i++) {
                this.emitters[i].visible = true;
            }
        }
    }

    destroy() {
        if (this.game && this.timeEvent) {
            this.game.time.events.remove(this.timeEvent);
        }
        for (var i = 0; i < this.emitters.length; i++) {
            this.emitters[i].destroy();
        }

        //this.removeImage('rain2');
        super.destroy();
    }
}



