import * as _ from 'lodash';

/**
 * @typedef InteractionTarget
 * @type {object}
 * @property {function} bindInteraction 绑定事件
 * @property {function} unbindInteraction 解绑事件
 */

export default class Item {

    /**
     * @type {InteractionTarget[]}
     */
    interactionTargets = [];

    /**
     *
     * @param {InteractionTarget} target
     */
    add(target) {
        this.interactionTargets.push(target);
        target.bindInteraction.call(target);
    }

    /**
     *
     * @param {InteractionTarget} target
     */
    remove(target) {
        target.unbindInteraction.call(target);

        _.remove(this.interactionTargets, function(interactionTarget) {
            return interactionTarget === target;
        });
    }


    bind() {
        this.interactionTargets.forEach((target) => {
            if (target.bindInteraction) {
                target.bindInteraction.call(target);
            }
        });
    }

    unbind() {
        this.interactionTargets.forEach((target) => {
            if (target.unbindInteraction) {
                target.unbindInteraction.call(target);
            }
        });
    }

    destory() {
        this.interactionTargets = null;
    }
}



