export default class TweenHelper {
    /**
     *
     * @param {Phaser.Tween} tween
     * @param {boolean} isSpeedUp
     */
    static fixSpeed(tween, isSpeedUp) {
        // 修改延迟时间
        for(let i = 0;i < tween.timeline.length; i++) {
            let tweenData = tween.timeline[i];
            let startTime = tweenData.startTime - tweenData.delay;
            let delay = isSpeedUp ? tweenData.delay / 10 : tweenData.delay;
            tweenData.startTime = startTime + delay;
        }
        tween.timeScale = isSpeedUp ? 10 : 1;
        // console.log(`isSpeedUp: ${isSpeedUp}, animation: `, tween.timeScale);
    }
}



