// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';
import ClickHandle from '../utils/click-handler';

import Interactor from '../core/interactor';

import * as Model from '../model/';
import Modeler from '../core/modeler';

import Scene from '../render/scene';
import Element from '../render/element';

const MODEL_KEY = '__MODEL__';

export default class ElementAnimator {
    /**
     * @type {Phaser.Game}
     */
    static game;
    /**
     * @type {Phaser.Tween[]}
     */
    static animations = [];
    /**
     * @type {Model.Animation.Item.Base[]}
     */
    static models = [];
    static typewriter;
    static isTypewriteCompleted = false;
    static runIndex = -1;
    static completedCount = 0;

    /**
     * @type {Scene.Normal}
     */
    static scene = null;

    static clickHandler = new ClickHandle();

    static isWaitInput = false;

    static isSpeedUp = false;

    static reset() {
        this.models = [];
        this.runIndex = -1;
        this.completedCount = 0;
        this.isTypewriteCompleted = false;
        if (this.lastAnimation) {
            _.set(this.lastAnimation, MODEL_KEY, null);
            this.lastAnimation = null;
        }
        this.clickHandler.reset();
        this.animations = [];
        this.typewriter = null;
        this.game.tweens.removeAll();
    }

    /**
     *
     * @param {Scene.Normal} scene
     */
    static setScene(scene) {
        this.scene = scene;
        this.game = scene.game;
        // this.reset();
    }

    static addTypewriter(typewriter) {
        this.typewriter = typewriter;
    }

    /**
     * @param {Model.Animation.Item.Base} model
     */
    static addAnimation(model) {
        this.models.push(model);
    }

    static hasAnimation() {
        return this.models.length > 0 || this.typewriter;
    }

    static createAnimation(model) {
        /**
         * @type {Phaser.Tween}
         */
        let animation = null;
        if (model.isElement()) {
            animation = this.createElementAnimation(this.scene.game, model);
        }
        this.animations.push(animation);
        return animation;
    }

    /**
     * @param {Phaser.Game} game
     * @param {Element.Base} element
     * @param {Model.Animation.Item.Element} model
     */
    static createElementAnimation(game, model) {
        let element = this.scene.findElement(model.target.id);
        let isTypewriter = model.isTypewriter();
        let tweensData = model.getTweens();

        // 默认增加一个动画，用来计算整个元素动画时长
        let tween = game.add.tween({ sham: 0 });
        let duration = 0;

        _.map(tweensData, (tweenData) => {
            if (duration < tweenData.delay + tweenData.duration * (tweenData.repeat + 1)) {
                duration = tweenData.delay + tweenData.duration * (tweenData.repeat + 1);
            }

            let _tween = game.add.tween(element);
            // isFrom 代表的是 target指定的属性 是开始属性还是结束属性
            if (!model.trigger.isFrom) {
                _tween.from(tweenData.properties,
                    tweenData.duration,
                    Phaser.Easing.Linear.None,
                    false,
                    tweenData.delay + model.trigger.delay,
                    tweenData.repeat,
                    tweenData.yoyo,
                );
            } else {
                _tween.to(tweenData.properties,
                    tweenData.duration,
                    Phaser.Easing.Linear.None,
                    false,
                    tweenData.delay + model.trigger.delay,
                    tweenData.repeat,
                    tweenData.yoyo,
                );
            }

            tween.onStart.addOnce(() => {
                _tween.start();
            });

            this.fixSpeed(_tween);

            // 这些动画也要监控
            this.animations.push(_tween);
        });

        tween.to({ sham: 1 }, duration, Phaser.Easing.Linear.None, false);
        _.set(tween, MODEL_KEY, model);
        if (isTypewriter) {
            tween.onComplete.add(this.onTypewriterCompleteHandle, this);
        }
        else {
            tween.onStart.add(this.onAnimationStartHandle, this);
            tween.onComplete.add(this.onAnimationCompleteHandle, this);
        }
        this.fixSpeed(tween);
        return tween;
    }

    static getNext() {
        var model = null;
        let nextIndex = this.runIndex + 1;
        if (this.models.length > 0 && nextIndex < this.models.length) {
            model = this.models[nextIndex];
        }
        return model;
    }

    static fixSpeed(tween) {
        // 修改延迟时间
        for (let i = 0; i < tween.timeline.length; i++) {
            let tweenData = tween.timeline[i];
            let startTime = tweenData.startTime - tweenData.delay;
            let delay = this.isSpeedUp ? tweenData.delay / 10 : tweenData.delay;
            tweenData.startTime = startTime + delay;
        }
        tween.timeScale = this.isSpeedUp ? 10 : 1;
        // console.log(`isSpeedUp: ${this.isSpeedUp}, animation: `, tween.timeScale);
    }

    static speedUp() {
        // console.log('Animator: 加速');
        this.isSpeedUp = true;
        // 加速
        for (let i = 0; i < this.animations.length; i++) {
            this.fixSpeed(this.animations[i]);
        }
    }

    static speedNormal() {
        // console.log('Animator: 正常速度');
        this.isSpeedUp = false;
        // 减速
        for (let i = 0; i < this.animations.length; i++) {
            this.fixSpeed(this.animations[i]);
        }
    }

    static startTypewriter() {
        Interactor.addTarget(this);
        if (this.typewriter) {
            let typewriter = this.createAnimation(this.typewriter);
            typewriter.start();
        }
    }

    static startAnimation() {
        // 没有动画直接结束
        if (!this.hasAnimation()) {
            this.onComplete.dispatch();
            return;
        }
        this.runIndex = -1;
        this.completedCount = 0;
        let nextModel = this.getNext();

        if (nextModel) {
            let nextAnimation = this.createAnimation(nextModel);
            if (nextAnimation) {
                if (!Modeler.game.setting.isAutoPlay && nextModel.isTriggerClick()) {
                    this.bindClickHandle(nextAnimation.start, nextAnimation);
                } else {
                    nextAnimation.start();
                }
            }
        }
    }

    static changeTypewriterAnimationFlag() {
        this.isTypewriteCompleted = true;
    }

    static update() {
        if (Modeler.game.setting.isAutoPlay && this.clickHandler.isEnable) {
            this.clickHandler.onClickHandle.call(this.clickHandler);
        }
    }

    static onAnimationStartHandle(target, animation) {
        // 下一个动画的索引
        this.runIndex++;
        this.lastAnimation = animation;
        var nextModel = this.getNext();

        if (nextModel) {
            if (nextModel.isTriggerStart()) {
                let nextAnimation = this.createAnimation(nextModel);
                if (nextAnimation) {
                    nextAnimation.start();
                }
            }
        }
    }

    static onAnimationCompleteHandle(target, animation) {
        this.completedCount++;
        // 动画时间过短的话，没有触发 animation.onStart ，
        // 就直接 animation.onComplete, 这里加上修正
        if (!animation._hasStarted) {
            animation.onStart.dispatch(animation.target, animation);
            // this.onAnimationStartHandle(target, animation);
        }

        let model = _.get(animation, MODEL_KEY);
        let lastModel = _.get(this.lastAnimation, MODEL_KEY);

        if (this.hasCompleted()) {
            _.set(animation, MODEL_KEY, null);
            this.onCompleteHandle();
        } else if (lastModel && model.id === lastModel.id) {//---------------------后开始的动画不一定后结束---------------------------

            var nextModel = this.getNext();

            if (nextModel) {
                let nextAnimation = this.createAnimation(nextModel);
                if (nextModel.isTriggerEnd()) {
                    nextAnimation.start();
                } else if (nextModel.isTriggerClick()) {
                    if (!Modeler.game.setting.isAutoPlay) {
                        this.bindClickHandle(nextAnimation.start, nextAnimation);
                    } else {
                        nextAnimation.start();
                    }
                }
            }
        } else {
            _.set(animation, MODEL_KEY, null);
        }
    }

    static onTypewriterCompleteHandle(target, animation) {
        // 动画时间过短的话，没有触发 animation.onStart ，
        // 就直接 animation.onComplete, 这里加上修正
        if (!animation._hasStarted) {
            animation.onStart.dispatch(animation.target, animation);
            // this.onAnimationStartHandle(target, animation);
        }
        this.isTypewriteCompleted = true;//打字机动画完成后更改标志位
        if (this.hasCompleted()) {
            this.onCompleteHandle();
        }
    }

    static hasCompleted() {
        return this.completedCount == this.models.length && this.isTypewriteCompleted;//打字机动画和元素动画都完成才算完成
    }

    static onComplete = new Phaser.Signal();
    static onCompleteHandle() {
        this.onComplete.dispatch();
    }

    static pause() {
        for (let i = 0; i < this.animations.length; i++) {
            this.animations[i].pause();
        }
    }

    static resume() {
        for (let i = 0; i < this.animations.length; i++) {
            this.animations[i].resume();
        }
    }

    static bindClickHandle(listener, listenerContext) {
        this.clickHandler.bind(listener, listenerContext);
        // this.scene.waitUserInput = true;
        this.scene.baseGroup.onChildInputDown.addOnce(this.clickHandler.onClickHandle, this.clickHandler);
    }

    static bindInteraction() {
        if (this.clickHandler.isEnable) {
            this.scene.baseGroup.onChildInputDown.addOnce(this.clickHandler.onClickHandle, this.clickHandler);
        }
    }
    static unbindInteraction() {
        if (this.clickHandler.isEnable) {
            this.scene.baseGroup.onChildInputDown.remove(this.clickHandler.onClickHandle, this.clickHandler);
        }
    }

}



