// @ts-check
import * as _ from 'lodash';

import Interactor from './interactor';
import * as Model from '../model';
import AudioPlayer from './audioplayer';

export default class Modeler {
    static game = Model.game;
    /**
     * @type {Model.Material.List}
     */
    static material;
    static theme = Model.Theme;
    static story = Model.Story;

    static init(options) {
        this.game.setId(options.gameId);
    }

    static initGame(data) {
        this.game.formData(data);

        AudioPlayer.changeVolume('bgm', this.game.setting.audio.bgmVolume);
        AudioPlayer.changeVolume('effect', this.game.setting.audio.effectVolume);
        AudioPlayer.changeVolume('voice', this.game.setting.audio.voiceVolume);

        let bindInteractor = function(id, paramskey, gameKey) {
            Interactor.bindConfigure(id, function(interaction){
                interaction.params.set(paramskey, _.get(Modeler.game, gameKey));
            });
            Interactor.bindTrigger(id, function(interaction){
                _.set(Modeler.game, gameKey, interaction.params.get(paramskey));
                if (gameKey === 'setting.audio.bgmVolume') {
                    AudioPlayer.changeVolume('bgm', _.get(Modeler.game, gameKey));
                }
                if (gameKey === 'setting.audio.effectVolume') {
                    AudioPlayer.changeVolume('effect', _.get(Modeler.game, gameKey));
                }
                if (gameKey === 'setting.audio.voiceVolume') {
                    AudioPlayer.changeVolume('voice', _.get(Modeler.game, gameKey));
                }
            });
        };

        bindInteractor(Model.Interaction.Id.SET_AUTO_PLAY, 'isOn', 'setting.isAutoPlay');
        bindInteractor(Model.Interaction.Id.SET_FULL_SCREEN, 'isOn', 'setting.screen.isFull');
        bindInteractor(Model.Interaction.Id.SET_BGM_VOLUME, 'value', 'setting.audio.bgmVolume');
        bindInteractor(Model.Interaction.Id.SET_EFFECT_VOLUME, 'value', 'setting.audio.effectVolume');
        bindInteractor(Model.Interaction.Id.SET_VOICE_VOLUME, 'value', 'setting.audio.voiceVolume');

    }

    static initMaterial(data) {
        this.material = new Model.Material.List(data);
    }

    static initTheme(data) {
        this.theme.formData(data);
    }

    static initStory(data) {
        this.story.formData(data);
    }

    static reset() {
        this.game.reset();
        this.story.reset();
    }
}







