
import * as Model from '../../model';

import Factory from './factory';

import Base from './base';
import Background from './background';
import Figure from './figure';
import Button from './button';
import Saved from './saved';
import Slider from './slider';
import Switcher from './switcher';
import Playback from './playback';
import Name from './name';
import Dialogue from './dialogue';
import Option from './option';

let factory = new Factory();
factory.set(Model.Element.Item.Type.BACKGROUND, Background);
factory.set(Model.Element.Item.Type.FIGURE, Figure);
factory.set(Model.Element.Item.Type.BUTTON, Button);
factory.set(Model.Element.Item.Type.SAVED, Saved);
factory.set(Model.Element.Item.Type.SLIDER, Slider);
factory.set(Model.Element.Item.Type.SWITCHER, Switcher);
factory.set(Model.Element.Item.Type.PLAYBACK, Playback);
factory.set(Model.Element.Item.Type.NAME, Name);
factory.set(Model.Element.Item.Type.DIALOGUE, Dialogue);
factory.set(Model.Element.Item.Type.OPTION, Option);

export {
    factory,
    Base,
    Background,
    Figure,
    Button,
    Saved,
    Slider,
    Switcher,
    Playback,
    Name,
    Dialogue,
    Option,
};



