"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
let localStorage = typeof window != 'undefined' && window.localStorage
    ? window.localStorage
    : null;
if (localStorage === null) {
    throw new Error('Storage Manager: localStorage is not exists');
}
class StorageManager {
    static init(gameId) {
        this.gameId = gameId;
    }
    static getStorageKey(index) {
        // @todo 增加游戏id、用户信息
        return "Q4GameSaved|" + this.gameId + "|" + index;
    }
    static load(index) {
        let key = this.getStorageKey(index);
        let result = null;
        try {
            result = localStorage.getItem(key);
        }
        catch (e) {
            console.log("Storage Manager: localStorage 获取失败");
            return null;
        }
        return result;
    }
    static save(index, data) {
        let key = this.getStorageKey(index);
        let isOK = true;
        try {
            let tmp = localStorage.getItem(key);
            if (!tmp) {
                localStorage.removeItem(key);
            }
            localStorage.setItem(key, data);
        }
        catch (e) {
            isOK = false;
            console.log("Storage Manager: localStorage 存储失败");
        }
        return isOK;
    }
}
exports.default = StorageManager;



