// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';

import Base from './base';
import Animation from './animation';
import * as Material from '../material';

export default class Background extends Animation {
    /**
     * @type {Model.Element.Item.Background}
     */
    model;

    materail;

    init() {
        // _.set(window, 'background', this);
        this.material = new Material.Image(this.game, this, Modeler.material.find(this.model.materialId).getMetainfo());
        // this.material.f
        if (this.model.horizontal) {
            this.material.scale.x *= -1;
        }
        if (this.model.vertical) {
            this.material.scale.y *= -1;
        }
    }

    destroy(destroyChildren, soft) {
        this.material = null;

        super.destroy(true, soft);
    }
}





