// @ts-check

import { Phaser } from '../../../utils/load-phaser';

export default class Base  {
    /**
     * @param {Phaser.Game} game
     * @param {Phaser.Image} outView
     * @param {Phaser.Image} inView
     */
    constructor(game, outView, inView) {
        this.game = game;
        this.outView = outView;
        this.inView = inView;
        this.onComplete = new Phaser.Signal();
    }

    run() {
        // nothing
    }

    complete() {
        this.outView.destroy();
        this.inView.destroy();
        this.onComplete.dispatch();
    }
}



