// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';

import * as Model from '../model/';
import Modeler from '../core/modeler';

import Scene from '../render/scene';

import EffectAnimation from './effect/index';

export default class SceneAnimator {
    /**
     * @type {Phaser.Game}
     */
    static game;

    /**
     * @type {Scene.Normal}
     */
    static scene = null;

    /**
     * @type {EffectAnimation.Base}
     */
    animation;

    static paused = false;

    static reset() {
        // this.animation = null;
    }

    /**
     *
     * @param {Scene.Normal} scene
     */
    static setScene(scene) {
        this.scene = scene;
        this.game = scene.game;
    }

    static hasAnimation() {
        return this.scene.model.hasEffectAnimation();
    }

    static start() {
        // 没有动画直接结束
        if (!this.hasAnimation()) {
            return;
        }

        let effectAnimationData = this.formatEffectAnimation(this.scene.model.effectAnimation);
        let animation = EffectAnimation.factory.create(this.game, effectAnimationData);
        animation.run();
        if (this.paused) {
            animation.pause();
        }
        this.animation = animation;
    }

    static formatEffectAnimation(effectAnimation) {
        let result = null;
        switch(effectAnimation.type) {
            case Model.Scene.Item.Normal.EffectAnimationType.RAIN:
                result = {
                    type: 'rain',
                };
                break;
            case Model.Scene.Item.Normal.EffectAnimationType.SNOW:
                result = {
                    type: 'snow',
                };
                break;
            case Model.Scene.Item.Normal.EffectAnimationType.MAPLE:
                result = {
                    type: 'maple',
                };
                break;
            case Model.Scene.Item.Normal.EffectAnimationType.PETAL:
                result = {
                    type: 'petal',
                };
                break;
        }
        return result;
    }

    static pause() {
        this.paused = true;
        if (this.animation) {
            this.animation.pause();
        }
    }

    static resume() {
        this.paused = false;
        if (this.animation) {
            this.animation.resume();
        }
    }

    static update() {
        // this.game.debug.spriteBounds(this.animation.emitter, '#ff0000');
    }

    static shutdown() {
        if(this.animation){
            this.animation.destroy();
        }
    }
}



