// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';

export default class Text extends Phaser.BitmapText {

    lingHeight = 36; //文字的行高。这个值是写死的

    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Material.Metainfo.Text} model
     * @param {Model.Game.Font} font
     */
    constructor(game, parent, model, font) {
        super(game, model.x, model.y, font.family, model.text, font.size);

        this.tint = font.color;

        parent.add(this);
        // @ts-ignore
        this.textHeight = font.lineHeight ? font.lineHeight : this.lingHeight;
    }

    clearGlyphs() {

        for (var i = 0; i < this._glyphs.length; i++) {
            this._glyphs[i].destroy();
        }

        this._glyphs = [];
        this.children = [];
    }

    /**
     * 每刷出一个字都会调一次这个方法
     * 设置行高问题
     *
     * @method Phaser.BitmapText.prototype.updateText
     * @private
     */
    updateText() {
        super.updateText();
        // if (!this.verticalSpacing) {
        //     return;
        // }
        // var currentLine = 0;
        // for (var i = 0; i < this.children.length; i++) {
        //     //判断当前children是在第几行
        //     if (this.text.charAt(i + 1) == '\n') {
        //         currentLine++;
        //     }
        //     console.log(currentLine);
        //     //按行数进行对y偏移
        //     this._glyphs[i].position.y += this.verticalSpacing * currentLine;
        // }
    }
}



