"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Collection = require("../base/collection");
const Item = require("./item");
class List extends Collection.List {
    constructor(list) {
        super();
        for (var i = 0; i < list.length; i++) {
            var item = Item.factory.create(list[i]);
            if (item) {
                // 目前只有一个 bgm
                if (item.type === Item.Base.Type.BGM) {
                    this.bgm = item;
                }
                else {
                    this.list.push(item);
                }
            }
        }
    }
    getMaterialIds() {
        let materialIds = [];
        this.list.forEach(function (item) {
            materialIds = _.concat(materialIds, item.getMaterialIds());
        });
        return materialIds;
    }
}
exports.default = List;



