// @ts-check
import Base from './base';

import Modeler from '../../core/modeler';

import Url from '../../utils/url';
export default class Init extends Base {
    /**
     * @type {Phaser.Text}
     */
    text;

    /**
     * @type {boolean}
     */
    isLoaded;

    /**
     * @type {any}
     */
    loaded = {};

    init() {
        super.init();

        this.text = null;
        this.isLoaded = false;
    }

    create() {
        this.game.stage.backgroundColor = '#000000';

        // 绑定加载事件
        this.game.load.onFileComplete.add(this.fileComplete, this);
        this.game.load.onLoadComplete.add(this.loadComplete, this);

        // 加载文件列表
        this.game.load.json('materials', Url.file('data/materials.json'));
        this.game.load.json('game', Url.file('data/game.json'));
        this.game.load.json('theme', Url.file('data/theme.json'));
        this.game.load.json('story', Url.file('data/story.json'));

        // 加载字体列表
        this.game.load.bitmapFont('font',
            Url.file('assets/font/font_0.png'),
            Url.file('assets/font/font.fnt')
        );

        //加载特效素材
        this.game.load.atlas('effect',
            Url.systemFile('images/effect-material.png'),
            Url.systemFile('images/effect-material.json'),
        );

        // 绘制加载提示
        this.text = this.game.add.text(0, 0, 'initing...', {fill: '#ffffff'}, this.baseGroup);
        this.text.fontSize = 14;

        this.game.load.start();
    }

    fileComplete(progress, cacheKey, success, totalLoaded, totalFiles) {
        var text = '';
        if (success) {
            switch (cacheKey) {
                case 'materials':
                    this.loaded[cacheKey] = this.game.cache.getJSON(cacheKey);
                    text = '要有光，开始构建情报';
                    break;
                case 'game':
                case 'theme':
                    this.loaded[cacheKey] = this.game.cache.getJSON(cacheKey);
                    text = '情报构建完毕，开始创建世界';
                    break;
                case 'story':
                    this.loaded[cacheKey] = this.game.cache.getJSON(cacheKey);
                    text = '世界创建完毕，开始装配角色';
                    break;
                case 'font':
                    text = '角色装配完毕，准备降临';
                    break;
            }
        } else {
            text = cacheKey + ' 数据加载失败';
        }

        this.text.setText(text);
    }

    loadComplete() {
        this.isloaded = true;

        try {
            Modeler.initMaterial(this.loaded['materials']);
            Modeler.initGame(this.loaded['game']);
            Modeler.initTheme(this.loaded['theme']);
            Modeler.initStory(this.loaded['story']);
        } catch(e) {
            if (window['console']) {
                console.error(e, e.stack);
            } else {
                throw e;
            }
        }

        this.game.load.onFileComplete.remove(this.fileComplete, this);
        this.game.load.onLoadComplete.remove(this.loadComplete, this);
    }

    render() {
        // 绘制完成后，进行场景跳转
        if (this.isloaded) {
            if (this.onRenderFinish.getNumListeners() > 0) {
                this.onRenderFinish.dispatch();
                console.log('Scene Init: rendered');
            }
        }
    }

    shutdown() {

        this.text.destroy();
        this.text = null;

        super.shutdown();
    }
}



