"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Collection = require("../../base/collection");
const Utils = require("../../utils");
// import * as Metainfo from './metainfo';
class Base extends Collection.Item {
    constructor(data) {
        super();
        this.id = -1;
        this.type = '';
        this.materialId = '';
        this.id = Utils.getInt(data, 'id', this.id);
        this.type = Utils.getString(data, 'type', this.type);
        this.materialId = Utils.getString(data, 'materialId', this.materialId);
        this.z = Utils.getInt(data, 'z', this.z);
        this.x = Utils.getInt(data, 'x', this.x);
        this.y = Utils.getInt(data, 'y', this.y);
        this.w = Utils.getInt(data, 'w', this.w);
        this.h = Utils.getInt(data, 'h', this.h);
        this.angle = Utils.getInt(data, 'angle', this.angle);
        this.opacity = Utils.getInt(data, 'opacity', this.opacity);
        this.horizontal = Utils.getBoolean(data, 'horizontal', this.horizontal);
        this.vertical = Utils.getBoolean(data, 'vertical', this.vertical);
    }
    getMaterialIds() {
        return [this.materialId];
    }
    halfW() {
        return this.w / 2;
    }
    halfH() {
        return this.h / 2;
    }
}
exports.default = Base;



