// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Modeler from '../../core/modeler';
import * as Model from '../../model';

import Base from './base';

export default class Factory {

    creators = {};

    set (key, creator) {
        this.creators[key] = creator;
    }

    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Element.Item.Base} element
     * @return {Base}
     */
    create(game, parent, element) {
        // 没有包含类型
        if (!element || !_.has(element, 'type')) {
            return null;
        }

        let type = _.get(element, 'type');

        if (!_.has(this.creators, type)) {
            console.log('Render Element Factory: create ' + type + ' not found', element);
            return null;
        }

        return new this.creators[element.type](game, parent, element);
    }
}



