// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Base from './base';

export default class Leaf extends Base {

    constructor(game) {
        super(game);
    }

    run() {
        this.start();
    }

    start() {
        /**
         * @type {Phaser.Game}
         */

        let game = this.game;

        let emitter = game.add.emitter(0, 0, 30);
        emitter.makeParticles('effect',['maple1.png', 'maple2.png','maple3.png','maple4.png','maple5.png','maple6.png','maple7.png']);

        emitter.width = game.width;
        emitter.angle = -30;

        emitter.gravity = 15;


        emitter.setYSpeed(150, 200);
        emitter.setXSpeed(-5, 5);

        emitter.minRotation = 45;
        emitter.maxRotation = 90;

        emitter.setAlpha(0.6, 1);

        emitter.setScale(-0.4, 0.7, 0.4, 0.7, 5000, Phaser.Easing.Sinusoidal.InOut, true);

        emitter.start(false, 6500, 250, 0);

        this.emitter = emitter;
    }

    pause() {
        if (this.emitter) {
            this.emitter.visible = false;
        }
    }

    resume() {
        if (this.emitter) {
            this.emitter.visible = true;
        }
    }

    destroy() {
        this.emitter.destroy();
        super.destroy();
    }
}



