"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
class Creator {
    constructor(ctor) {
        this.ctor = ctor;
    }
    create(data) {
        return new this.ctor(data);
    }
}
exports.Creator = Creator;
class Factory {
    constructor() {
        this.creators = {};
    }
    set(key, constructor) {
        this.creators[key] = new Creator(constructor);
    }
    create(data) {
        // 没有包含类型
        if (!data || !_.has(data, 'type')) {
            return null;
        }
        let type = _.get(data, 'type');
        if (!_.has(this.creators, type)) {
            console.log('Model Factory: create ' + type + ' not found', data);
            return null;
        }
        return this.creators[type].create(data);
    }
}
exports.Factory = Factory;



