// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Base from './base';

export default class Flash extends Base {
    constructor(game, data) {
        super(game);
        this.isBlack = !!_.get(data, 'isBlack', false);

        // 这里的数据转换应该移到model中去
        let color = _.get(data, 'color', '#ffffff');
        this.color = parseInt(color.replace('#', '0x'));
        this.times = _.get(data, 'times', 1);
        this.duration = _.get(data, 'duration', 1);
    }

    run() {
        this.circle = this.duration * 1000 / this.times;//周期

        this.mask = this.game.add.graphics(0, 0);
        this.mask.beginFill(this.color);
        this.mask.drawPolygon(
            { x: 0, y: 0 },
            { x: this.game.width, y: 0 },
            { x: this.game.width, y: this.game.height },
            { x: 0, y: this.game.height }
        );

        this.mask.alpha = 0;

        this.tween = this.game.add.tween(this.mask);

        this.timeEvent = this.game.time.events.add(200, this.startTween, this, this.tween);

        return this.tween;
    }

    fixSpeed(isSpeedUp) {
        if (this.tween) {
            this.fixTweenspeed(this.tween, isSpeedUp);
        }
    }

    startTween(tween) {
        this.mask.alpha = 1;
        tween.to(
            { alpha: 0 },
            this.circle / 2,//动画时长为1/2周期
            Phaser.Easing.Linear.None,
            true
        );

        tween.repeat(this.times - 1, this.circle / 2);
        tween.onComplete.addOnce(this.complete, this);
    }

    complete() {
        if (this.mask) {
            this.mask.destroy();
        }
        if (this.timeEvent) {
            this.game.time.events.remove(this.timeEvent);
        }
        if(this.tween){
            this.game.tweens.remove(this.tween);
        }
        super.complete();
    }

    pause() {
        if (this.mask) {
            this.mask.visible = false;
        }
        if(this.tween){
            this.tween.pause();
        }
    }

    resume() {
        if (this.mask) {
            this.mask.visible = true;
        }
        if(this.tween){
            this.tween.resume();
        }
    }
}



