"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Collection = require("../base/collection");
const Utils = require("../utils");
const Scene = require("../scene");
class Item extends Collection.Item {
    constructor(data) {
        super();
        this.id = -1;
        this.name = '';
        this.preloads = [];
        this.startSceneId = 1;
        this.id = Utils.getInt(data, 'id', this.id);
        this.name = Utils.getString(data, 'name', this.name);
        this.preloads = Utils.getList(data, 'preloads', []);
        this.scene = new Scene.List(Utils.getList(data, 'scenes', []));
        this.startSceneId = Utils.getInt(data, 'startSceneId', this.startSceneId);
        this.reset();
    }
    reset() {
        this.scene.setNextId(this.startSceneId);
    }
    hasScene() {
        return this.scene.length > 0;
    }
    findPreloadMaterialIds(scene) {
        let preload = _.find(this.preloads, function (preload) {
            return _.indexOf(preload.scenes, scene.id) !== -1;
        });
        let materialIds = [];
        if (preload) {
            materialIds = preload.materials;
        }
        return materialIds;
    }
}
exports.default = Item;



