// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../../utils/load-phaser';

import Base from './base';

export default class Factory {

    creators = {};

    add(type, creator) {
        this.creators[type] = creator;
    }

    /**
     *
     * @param {Phaser.Game} game
     * @return {Base}
     */
    create(game, outView, inView, data) {
        // 没有包含类型
        if (!data || !_.has(data, 'type')) {
            return null;
        }

        let type = _.get(data, 'type');

        if (!_.has(this.creators, type)) {
            console.log('Transition Factory: create ' + type + ' not found', type);
            return null;
        }

        return new this.creators[type](game, outView, inView, data);
    }
}



