        // if (storySceneStack) {
        //     this.manul.director.setStorySceneStack(storySceneStack.split(","));
        // }
        // 恢复保存游戏数值
        // this.manul.gameDataManager.game.formSavedVariables(saved.get(Item.Key.gameVariables));
