"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Collection = require("../../base/collection");
const Item_1 = require("./Item");
class List extends Collection.List {
    constructor(list) {
        super();
        for (var i = 0; i < list.length; i++) {
            var item = new Item_1.default(list[i]);
            this.list[i] = item;
        }
    }
    reset() {
        for (var i = 0; i < this.list.length; i++) {
            this.list[i].reset();
        }
    }
    fromSaved(saved) {
        let savedItems = JSON.parse(saved);
        if (_.isArray(savedItems)) {
            for (var i = 0; i < savedItems.length; i++) {
                var savedItem = savedItems[i];
                var item = this.find(savedItem.id);
                if (item) {
                    item.fromSaved(savedItem.value);
                }
            }
        }
        else {
            console.log('Variable List: 解析存档失败');
        }
    }
    toSaved() {
        var SavedItems = [];
        for (var i = 0; i < this.list.length; i++) {
            var item = this.list[i];
            SavedItems[i] = { id: item.id, value: item.toSaved() };
        }
        return JSON.stringify(SavedItems);
    }
    changeSaved() {
        for (var i = 0; i < this.list.length; i++) {
            this.list[i].changeSaved();
        }
    }
    checkLogic(logic) {
        var item = this.find(logic.variableId);
        // 找不到变量
        if (!item) {
            console.log("Variable List: 用户 variables " + logic.variableId + "找不到");
            return;
        }
        var result = item.check(logic.operator, logic.value);
        console.log('Variable List: 条件检测 variable ' + logic.variableId + ' 检测 (' + logic.operator + ',' + logic.value + ') 为 ' + result);
        return result;
    }
}
exports.default = List;



