// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';

import * as Model from '../model/';
import Modeler from '../core/modeler';

export default class Sound extends Phaser.Sound {

    onStart = new Phaser.Signal();
    onComplete = new Phaser.Signal();

    /**
     * @type {Model.Audio.Item.Effect}
     */
    model = null;

    loopCount = 0;
    /**
     * @type {Phaser.TimerEvent}
     */
    timerEvent = null;

    static getLoop(model) {
        return model.isLoop ? true : model.times > 1;
    }

    constructor(game, key, volume, model) {
        super(game, key, volume, Sound.getLoop(model));
        this.model = model;
        this.onLoop.add(this.hasLooped, this);
    }

    start() {
        if (this.model.delay) {
            this.timerEvent = this.game.time.events.add(this.model.delay, this.delay, this);
        } else {
            this.onStart.dispatch(this);
            this.play();
        }
    }

    delay() {
        if (this.timerEvent) {
            this.game.time.events.remove(this.timerEvent);
        }
        this.onStart.dispatch(this);
        this.play();
    }

    stop() {
        try {
            super.stop();
        } catch(e) {
            // nothing
        }

        this.onComplete.dispatch(this);
    }

    hasLooped() {
        if (this.model.isLoop) {
            return;
        }

        this.loopCount++;

        if(this.loopCount == this.model.times) {
            this.stop();
        }
    }

    destory() {
        if (this.timerEvent) {
            this.game.time.events.remove(this.timerEvent);
        }
        super.destroy();
    }
}



