import Item from './item';

export default class List {
    /**
     * @type {Item[]}
     */
    items = [];
     /**
     * @type {Item}
     */
    current = null;

    constructor() {
        // 默认交互层
        this.current = new Item();
    }

    reset() {
        if (this.current) {
            this.popdownAll();
        }

        this.items = [];
        this.current = null;
        // 默认交互层
        this.current = new Item();
    }

    popup() {

        if (this.current) {
            this.current.unbind();
            this.items.push(this.current);
        }

        this.current = new Item();
    }

    popdown() {
        // 保证默认交互层正确处理
        if (this.current) {
            this.current.unbind();
            this.current.destory();

            this.current = this.items.pop();
            if (this.current) {
                this.current.bind();
            }
        }
    }

    popdownAll() {
        do {
            this.popdown();
        } while(this.current);
    }

    getCurrent() {
        return this.current;
    }

    /**
     *
     * @param {InteractionTarget} target
     */
    add(target) {
        this.current.add(target);
    }

    /**
     *
     * @param {InteractionTarget} target
     */
    remove(target) {
        this.current.remove(target);
    }

    bind() {
        this.current.bind();
    }

    unbind() {
        this.current.unbind();
    }
}



