// @ts-check
import * as _ from 'lodash';

import Modeler from './modeler';
import * as Model from '../model';

export default class Loader {
    /**
     * @type {{[index:string]:boolean}}
     */
    static loaded = {};

    static isLoaded(loadinfos) {
        return false;
    }

    static getTemplateLoadinfos() {
        let materialIds = Modeler.theme.template.getAllMaterialIds();

        let loadinfos = this.getLoadinfos(materialIds);

        loadinfos = loadinfos.filter((loadinfo) => {
            return !_.has(this.loaded, loadinfo.key);
        });

        return loadinfos;
    }

    static getStoryLoadinfos(scene) {
        let materialIds = Modeler.story.section.getCurrent().findPreloadMaterialIds(scene);

        let loadinfos = this.getLoadinfos(materialIds);

        loadinfos = loadinfos.filter((loadinfo) => {
            return !_.has(this.loaded, loadinfo.key);
        });

        return loadinfos;
    }

    /**
     * 通过 materialIds 获取需要加载的 Loadinfos
     * @param {string[]} materialIds
     */
    static getLoadinfos(materialIds) {
        /**
         * @type {Model.Loadinfo[]}
         */
        let loadinfos = [];

        materialIds.forEach(function(materialId) {
            let material = Modeler.material.find(materialId);

            if (material) {
                loadinfos = _.concat(loadinfos, material.getLoadinfos());
            }
        });

        return loadinfos;
    }

    static update(cacheKey, success) {
        this.loaded[cacheKey] = true;
    }
}



