"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Collection = require("../../base/collection");
const Struct = require("../../lhotse/src/struct");
const Utils = require("../../utils");
// import * as Metainfo from './metainfo';
class Base extends Collection.Item {
    constructor(data) {
        super();
        this.id = -1;
        this.type = '';
        this.id = Utils.getInt(data, 'id', this.id);
        this.type = Utils.getString(data, 'type', this.type);
    }
    isCondition() {
        return this.type === Struct.Scene.Type.CONDITION;
    }
    isOperation() {
        return this.type == Struct.Scene.Type.OPERATION;
    }
    isEnd() {
        return this.type == Struct.Scene.Type.END;
    }
    isJump() {
        return this.type == Struct.Scene.Type.JUMP;
    }
    isSketch() {
        return this.type == Struct.Scene.Type.SKETCH;
    }
}
exports.default = Base;



