// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Base from './base';

export default class Petal extends Base {

    constructor(game) {
        super(game);
    }

    run() {
        this.start();
    }

    start() {
        /**
         * @type {Phaser.Game}
         */

        let game = this.game;

        let emitter = game.add.emitter(0, 0, 30);
        emitter.makeParticles('effect', ['petal1.png','petal2.png', 'petal3.png', 'petal4.png', 'petal5.png', 'petal6.png', 'petal7.png']);

        emitter.width = game.width;

        emitter.angle = -28;

        emitter.gravity = 50;

        // emitters[i].minParticleScale = 1.2;
        // emitters[i].maxParticleScale = 1.4;

        emitter.setYSpeed(100, 180);
        emitter.setXSpeed(-50, 50);

        emitter.setAlpha(0.8, 1);

        emitter.minRotation = 45;
        emitter.maxRotation = 180;

        emitter.setScale(-1.0, 1.2, 1.0, 1.2, 3000, Phaser.Easing.Sinusoidal.InOut, true);

        emitter.start(false, 6000, 200, 0);

        this.emitter = emitter;
    }

    pause() {
        if (this.emitter) {
            this.emitter.visible = false;
        }
    }

    resume() {
        if (this.emitter) {
            this.emitter.visible = true;
        }
    }

    destroy() {
        this.emitter.destroy();

        this.removeImage('petal1');
        this.removeImage('petal2');
        this.removeImage('petal3');
        super.destroy();
    }
}



