// @ts-check
import Base from './base';

import Modeler from '../../core/modeler';
import * as Model from '../../model';

import Url from '../../utils/url';

import * as Element from '../element';

export default class Sketch extends Base {
    /**
     * @type {Model.Scene.Item.Sketch}
     */
    model;

    init(model) {
        super.init();

        this.model = model;
    }

    create() {
        if (this.model.hasElement()) {
            for(let i = 0; i < this.model.element.list.length; i++) {
                let element = this.model.element.list[i];
                Element.factory.create(this.game, this.baseGroup, element);
            }
        }
    }

    shutdown() {
        this.model = null;

        super.shutdown();
    }
}



