// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';

export default class Base extends Phaser.Group {
    /**
     * @type {Phaser.Group}
     */
    parent;

    model;

    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Element.Item.Base} model
     */
    constructor(game, parent, model, ...args) {
        super(game, parent, model.id.toString());
        this.model = model;

        this.x = model.x;
        this.y = model.y;
        this.width = model.w;
        this.height = model.h;

        this.init(...args);

        this.angle = model.angle;


        this.alpha = model.opacity / 100;

    }

    init(...args) {
        // nothing
    }
}



