// @ts-check
import * as _ from 'lodash';

import { Phaser, PIXI } from '../../utils/load-phaser';

import Interactor from '../../core/interactor';

/**
 * 可滚动的区域
 * @extends Phaser.Group
 */
export default class Scrollable extends Phaser.Group {

    constructor(game, parent, horizontally, vertically) {
        super(game, parent, 'Scrollable');

        // @todo 先不考虑水平滚动，以后再说
        this.horizontally = false; // 水平滚动
        this.vertically = true; // 垂直滚动

        // 滚动的距离
        this.scrollSetp = 30;

        // 记录最原始的坐标
        this.originX = 0;
        this.originY = 0;

        // Y轴滚动限制
        this.minScrollY = 0;
        this.maxScrollY = 0;

        this.isMoving = false;
        this.moveOriginPointer = null;
    }

    scrollBottom() {
        this.y = this.originY - (this.height - this.mask.height);
    }

    setMaskRect(x, y, w, h) {
        var mask = this.game.make.graphics(0, 0);
        // @ts-ignore
        this.parent.add(mask);
        mask.beginFill(0xffffff);
        mask.drawRect(x, y, w, h);
        mask.endFill();
        this.mask = mask;
        this.originY = this.y;
    }

    bindInteraction() {
        // this.originY = this.y;
        this.minScrollY = this.mask.y;
        this.maxScrollY = this.height - this.mask.height;
        if (this.height > this.mask.height) {
            // console.log('g.h %d, m.h %d, scroll.h %d', this.height, this.mask.height, this.maxScrollY);
            // 绑定滚动事件
            this.game.input.mouse.mouseWheelCallback = this.mouseWheelHandle.bind(this);
            // 绑定鼠标拖拽事件
            this.game.input.addMoveCallback(this.onMoveHandler, this);
            this.game.input.onDown.add(this.onChildInputDownHandler, this);
            this.game.input.onUp.add(this.onChildInputUpHandler, this);
        }
    }

    unbindInteraction() {
        if (this.height > this.mask.height) {
            this.game.input.mouse.mouseWheelCallback = null;
            this.game.input.deleteMoveCallback(this.onMoveHandler, this);
            this.game.input.onDown.remove(this.onChildInputDownHandler, this);
            this.game.input.onUp.remove(this.onChildInputUpHandler, this);
        }
    }

    mouseWheelHandle(event) {
        var offset = this.scrollSetp;
        this.scrollY(event.deltaY, offset);
    }

    scrollY(deltaY, offset) {

        let scrollOffsetY;
        if (deltaY > 0) {
            // 往上
            scrollOffsetY = this.y - this.originY - offset;
            if (scrollOffsetY < -this.maxScrollY) {
                scrollOffsetY = -this.maxScrollY;
            }
        } else {
            // 往下
            scrollOffsetY = this.y - this.originY + offset;
            if (scrollOffsetY > this.minScrollY) {
                scrollOffsetY = this.minScrollY;
            }
        }

        // if (!this.checkScrollScrollY(scrollOffsetY)) {
        //     return;
        // }

        this.y = this.originY + scrollOffsetY;
    }

    checkScrollScrollY(scrollOffsetY) {
        // y轴滚动即在Y轴 -this.maxScrollY 和 this.minScrollY（0） 的区间内做滚动
        if (scrollOffsetY > this.minScrollY) {
            return false;
        }

        if (scrollOffsetY < -this.maxScrollY) {
            return false;
        }

        return true;
    }

    onMoveHandler(pointer, x, y) {
        //console.log('move');
        if (this.isMoving && this.moveOriginPointer) {

            var deltaY = pointer.y - this.moveOriginPointer.y;

            this.scrollY(deltaY, Math.abs(deltaY));
        }
    }

    onChildInputDownHandler(sprite, pointer) {
        this.isMoving = true;
        this.moveOriginPointer = pointer;
    }

    onChildInputUpHandler(sprite, pointer) {
        this.isMoving = false;
    }

    destroy(destroyChildren, soft) {

        this.unbindInteraction();

        this.moveOriginPointer = null;
        // this.mask.destroy(true);
        this.mask = null;

        super.destroy(true, soft);
    }

}



