"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Collection = require("../../base/collection");
var Key;
(function (Key) {
    Key[Key["version"] = 0] = "version";
    Key[Key["sectionId"] = 1] = "sectionId";
    Key[Key["sceneId"] = 2] = "sceneId";
    Key[Key["gameVariables"] = 3] = "gameVariables";
    Key[Key["storySceneStack"] = 4] = "storySceneStack";
    Key[Key["time"] = 5] = "time";
    Key[Key["name"] = 6] = "name";
    Key[Key["thumbnail"] = 7] = "thumbnail";
})(Key || (Key = {}));
;
class Item extends Collection.Item {
    constructor() {
        super();
        this.isUsableVersion = function () {
            return this.data[Item.Key.version] == Item.engineVersion;
        };
        this.toStorageData = function () {
            return !this.isNull() ? this.data.join(Item.splitSympol) : '';
        };
        this.fromStorageData = function (storageData) {
            var data = storageData.split(Item.splitSympol);
            if (data.length <= 1) {
                return;
            }
            if (Item.engineVersion == data[Item.Key.version]) {
                // data[Item.Key.sectionId] = parseInt(data[Item.Key.sectionId]);
                // data[Item.Key.sceneId] = parseInt(data[Item.Key.sceneId]);
                data[Item.Key.time] = parseInt(data[Item.Key.time]);
            }
            this.data = data;
        };
        this.data = [Item.engineVersion];
    }
    get(k) {
        return this.data[k];
    }
    ;
    set(k, v) {
        if (k == Item.Key.version) {
            return;
        }
        this.data[k] = v;
    }
    ;
    isNull() {
        return this.data.length == 1; // 默认只有version存在
    }
    ;
}
Item.Key = Key;
// 每次修改该引擎存档相关逻辑都要修改这个版本号
Item.engineVersion = '0.04';
Item.splitSympol = '|@|';
exports.default = Item;



