// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';

import * as Model from '../model/';
import Modeler from '../core/modeler';

import Scene from '../render/scene';

import SceneAnimation from './scene/index';

export default class SceneAnimator {
    /**
     * @type {Phaser.Game}
     */
    static game;

    /**
     * @type {SceneAnimation.Base}
     */
    static animations = [];
    /**
     * @type {Scene.Normal}
     */
    static scene = null;
    static animationCount = 0;

    /**
     * @type {Model.Scene.SceneAnimation}
     */
    static sceneAnimationModel;

    static reset() {
        this.animations = [];
        this.animationCount = 0;
    }

    /**
     *
     * @param {Scene.Normal} scene
     */
    static setScene(scene) {
        this.scene = scene;
        this.game = scene.game;
        this.sceneAnimationModel = scene.model.sceneAnimation;
    }

    static hasAnimation() {
        let sceneAnimation = this.scene.model.sceneAnimation;
        return sceneAnimation && sceneAnimation.hasAnimation();
    }

    static speedUp() {
        // console.log('Animator: 加速');
        this.isSpeedUp = true;
        // 加速
        for (let i = 0; i < this.animations.length; i++) {
            this.animations[i].fixSpeed(this.isSpeedUp);
        }
    }

    static speedNormal() {
        // console.log('Animator: 正常速度');
        this.isSpeedUp = false;
        // 减速
        for (let i = 0; i < this.animations.length; i++) {
            this.animations[i].fixSpeed(this.isSpeedUp);
        }
    }

    static start() {
        // 没有动画直接结束
        if (!this.hasAnimation()) {
            this.onComplete.dispatch();
            return;
        }

        this.startFlash();
        this.startShake();
    }

    static startFlash() {
        if (this.sceneAnimationModel.flash) {
            let flash = new SceneAnimation.Flash(this.game, this.sceneAnimationModel.flash);
            flash.onComplete.addOnce(this.onFlashCompleteHandle, this, 0, flash);
            flash.run();
            this.animationCount++;//动画开始计数加1
            flash.fixSpeed(this.isSpeedUp);
            this.animations.push(flash);
        }
    }

    static startShake() {
        if (this.sceneAnimationModel.shake) {
            let shake = new SceneAnimation.Shake(this.game, this.sceneAnimationModel.shake);
            shake.onComplete.addOnce(this.onShakeCompleteHandle, this, 0, shake);
            shake.run();
            this.animationCount++;//动画开始计数加1
            shake.fixSpeed(this.isSpeedUp);
            this.animations.push(shake);
        }
    }

    static onFlashCompleteHandle(animation) {
        _.remove(this.animations, function (e) {
            return e == animation;
        })
        this.onCompleteHandle();
    }

    static onShakeCompleteHandle(animation) {
        _.remove(this.animations, function (e) {
            return e == animation;
        })
        this.onCompleteHandle();
    }

    static onComplete = new Phaser.Signal();
    static onCompleteHandle() {
        this.animationCount--;//动画完成计数减1
        //保证所有动画完成时再执行
        if (this.animationCount == 0) {
            this.onComplete.dispatch();
        }
    }

    static pause() {
        for (let i = 0; i < this.animations.length; i++) {
            this.animations[i].pause();
        }
    }

    static resume() {
        for (let i = 0; i < this.animations.length; i++) {
            this.animations[i].resume();
        }
    }
}



