"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Struct = require("../../lhotse/src/struct");
const Utils = require("../../utils");
const game_1 = require("../../game");
const base_1 = require("./base");
const SelfUtils = require("./utils");
class Operation extends base_1.default {
    constructor(data) {
        super(data);
        this.variableId = -1;
        this.next = -1;
        this.variableId = Utils.getInt(data, 'variableId', this.variableId);
        this.operator = Utils.getInt(data, 'operator', this.operator);
        this.target = new SelfUtils.Target(data.target);
        this.next = Utils.getInt(data, 'next', this.next);
    }
    run() {
        var variable = game_1.default.variable.find(this.variableId);
        // 找不到变量，不应用数值操作
        if (!variable) {
            console.log("Model Scene Operation: 用户 variables " + this.variableId + "找不到");
            return;
        }
        let operator = '';
        let value = this.target.getValue();
        switch (this.operator) {
            case Struct.Scene.Operation.Operator.ASSIGN:
                operator = '=';
                variable.set(value);
                break;
            case Struct.Scene.Operation.Operator.PLUS:
                operator = '+';
                variable.set(variable.get() + value);
                break;
            case Struct.Scene.Operation.Operator.MINUS:
                operator = '-';
                variable.set(variable.get() - value);
                break;
            case Struct.Scene.Operation.Operator.MULTIPLY:
                operator = '*';
                variable.set(variable.get() * value);
                break;
            case Struct.Scene.Operation.Operator.DIVIDE:
                operator = '/';
                // value不能为0，为0不进行计算
                if (value != 0) {
                    // 除法这里只保证没有小数
                    variable.set(variable.get() / value);
                }
                break;
        }
        console.log(`Model Scene Operation: 数值变化 ${variable.desc}(V${variable.id}) 应用 `
            + `(${operator},${this.target.toString()},${value}) 变为 ${variable.get()}`);
        game_1.default.variable.changeSaved();
        return this.next;
    }
}
exports.default = Operation;



