// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Image from './image';
import Text from './text';

import Playback from './playback';

export default class TextBox extends Phaser.Group {
    /**
     * @type {Model.Material.Metainfo.TextBox}
     */
    model;

    /**
     * @type {Phaser.Group}
     */
    parent;

    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Material.Metainfo.TextBox} model
     * @parma {{name:string, size:number, color:number}} font
     */
    constructor(game, parent, model, font, align) {
        super(game, parent, 'MaterialTextBox');
        this.init(model, font, align);
    }

    /**
     *
     * @param {Model.Material.Metainfo.TextBox} model
     * @parma {Model.Game.Font} font
     */
    init(model, font, align) {

        /**
         * @type {Model.Material.Metainfo.TextBox}
         */
        let metainfo = model;

        this.x = metainfo.x;
        this.y = metainfo.y;
        this.width = metainfo.w;
        this.height = metainfo.h;

        this.background = new Image(this.game, this, metainfo.subs.background);

        let textData = metainfo.subs.text;
        if (textData.text) {
            let renderText = new Text(this.game, this, textData, font);

            if (align != 'left') {
                // 需要计算下文本的宽度和高度
                renderText.updateTransform();

                // 重新定位居中
                renderText.position.x = (textData.w - renderText.textWidth) / 2;
                renderText.position.y = (textData.h - renderText.textHeight) / 2;
            }

            this.renderText = renderText;
        }
    }

    destroy(destroyChildren, soft) {

        this.background = null;

        super.destroy(true, soft);
    }
}





