// @ts-check
import { Phaser } from '../utils/load-phaser';

import Modeler from './modeler';
import Loader from './loader';
import Interactor from './interactor';
import Renderer from './renderer';
import Animator from './animator';
import AudioPlayer from './audioplayer';

import * as Model from '../model';

export default class director {
    static modeler = Modeler;
    static renderer = Renderer;
    static animator = Animator;
    /**
     * @type {Model.Options.Type}
     */
    static options;

    /**
     * @type {Phaser.Game}
     */
    static game;
    /**
     *
     * @param {Model.Options.Type} options
     * @param {Phaser.Game} game
     */
    static init(options, game) {
        this.options = options;
        this.game = game;
        this.modeler.init(options);
        this.initInteraction();
        this.renderer.init(game);
    }

    static initInteraction() {
        Interactor.init();

        Interactor.bindTrigger(
            Model.Interaction.Id.START_GAME,
            this.startStoryScene.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.GO_TITLE_SCENE,
            this.startFirstScene.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.JUMP_NEXT_SCENE,
            this.nextStoryScene.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.JUMP_OPTION_SCENE,
            this.nextOptionScene.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.SAVE_OR_LOAD_GAME,
            this.saveOrLoadGame.bind(this)
        );
    }


    static run() {
        this.renderer.renderInit(this.startFirstScene.bind(this));
    }

    static startFirstScene() {
        AudioPlayer.reset();
        AudioPlayer.bgm.stop();
        // @todo 游戏数据初始化
        Modeler.reset();
        this.renderer.reset();
        this.renderScene(this.renderer.Scene.TITLE);
    }

    static startStoryScene() {
        Modeler.story.start(this.options.sectionId, this.options.sceneId);

        // this.nextStoryScene();
        this.nextStoryScene();
    }

    /**
     *
     * @param {Model.Interaction} interaction
     */
    static nextOptionScene(interaction) {
        let next = interaction.params.get('next');
        console.log('Director: 选项选中切换场景', next);
        let section = Modeler.story.section.getCurrent();
        console.assert(section, 'Director: 选项选中切换场景时不能获取当前场景');
        section.scene.setNextId(next);

        this.nextStoryScene();
    }

    static nextStoryScene() {
        let scene = Modeler.story.getNextScene();
        if (scene === null || scene === undefined) {
            // 没有后续场景，自动播放停止
            this.modeler.game.setting.isAutoPlay = false;
            console.log('Director: 没有后续场景');
            return;
        }

        if (scene.type === Model.Scene.Item.Type.END) {
            console.log('Director: run ' + scene.type);
            AudioPlayer.bgm.stop();
            this.startFirstScene();
            return;
        }

        if (scene.type === Model.Scene.Item.Type.NORMAL) {
            this.renderScene(this.renderer.Scene.NORMAL, scene);
        }
    }

    /**
     *
     * @param {string} scene
     * @param {Model.Scene.Item.Base} [model]
     */
    static renderScene(scene, model) {
        /**
         * @type {Model.Loadinfo[]}
         */
        let loadinfos = [];

        if (scene === this.renderer.Scene.TITLE) {
            loadinfos = Loader.getTemplateLoadinfos();
        } else if (scene === this.renderer.Scene.NORMAL) {
            loadinfos = Loader.getStoryLoadinfos(model);
        }

        if (loadinfos.length > 0) {
            this.renderer.renderLoading(
                this.renderScene.bind(this, ...arguments),
                loadinfos,
                this.loadingUpdate.bind(this),
            );
            return;
        }

        if (scene === this.renderer.Scene.TITLE) {
            let template = Modeler.theme.template.findByType(Model.Template.Item.Type.TITLE);
            this.renderer.renderTitle(template.getDefaultScene());
            console.log('Director: render ' + scene);
        } else if (scene === this.renderer.Scene.NORMAL) {
            this.renderer.renderNormal(model);
            console.log('Director: render ' + model.type + ' ' + model.id);
        }
    }

    static loadingUpdate(...args) {
        Loader.update.apply(Loader, args);
    }

    /**
     *
     * @param {Model.Interaction} interaction
     */
    static saveOrLoadGame(interaction) {
        let isSaving = interaction.params.get('isSaving');
        let index = interaction.params.get('index');

        if (isSaving) {
            Modeler.game.save(index);
            // 关闭弹出的存档管理界面
            this.renderer.popdownUI(true);
        } else {
            let result = Modeler.game.load(index);
            if (result) {
                // 关闭弹出的存档管理界面
                this.renderer.popdownUI(true);
                this.nextStoryScene();
            }
        }
    }
}



