// @ts-check

import { Phaser } from '../../utils/load-phaser';
import TweenHelper from '../../utils/tween-helper';

export default class Base  {
    /**
     * @param {Phaser.Game} game
     */
    constructor(game) {
        this.game = game;
        this.onComplete = new Phaser.Signal();
    }

    run() {
        // nothing
    }

    fixSpeed(isSpeedUp) {
        // nothing
    }

    fixTweenspeed(tween, isSpeedUp) {
        TweenHelper.fixSpeed(tween, isSpeedUp);
    }

    pause() {
        // nothing
    }

    resume() {
        // nothing
    }

    complete() {
        this.onComplete.dispatch();
    }
}



