// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../../utils/load-phaser';
import { Signal } from 'signals';

import Base from './base';

import * as Model from '../../model';
import Url from '../../utils/url';

class ProgressBar extends Phaser.Group {
    /**
     * @type {Phaser.Text} 进度百分比
     */
    percent;

    /**
     * @type {Phaser.Graphics} 进度条前景
     */
    front;

    /**
     * @type {Phaser.Graphics} 进度条背景
     */
    back;

    /**
     * @type {Phaser.Text} 进度条处理提示语
     */
    tips;

    constructor(game, parent) {
        super(game, parent, 'ProgressBar');

        this.init();
    }

    init() {
        var game = this.game;


        var width = game.width;
        var height = game.height;
        // @todo 如果是手机横屏则需要额外处理
        let msr = _.get(this.game, 'msr');
        if (msr) {
            width = msr.width;
            height = msr.height;
        }

        // 绘制进度条背景
        this.back = game.add.graphics(0, height - 18, this);
        this.back.beginFill(0x000000, 0.35);
        this.back.drawRect(0, 0, width, 18);

        // 绘制进度条
        this.front = game.add.graphics(0, height - 18, this);
        this.front.beginFill(0x01a6be, 1);
        this.front.drawRect(0, 0, width, 18);
        this.front.scale.x = 0;

        // 百分比
        this.percent = game.add.text(0, height - 18, '0%', {fill: '#ffffff', boundsAlignH: "center", boundsAlignV: "middle"}, this);
        this.percent.fontSize = 12;
        this.percent.setTextBounds(0, 0, width, 18);

        // 提示语
        this.tips = game.add.text(0, height - 18 * 2, '', {fill: '#5ea49c'}, this);
        this.tips.fontSize = 12;
        this.tips.setShadow(1, 1, 'rgba(0,0,0,0.25)', 1);
    }


    /**
     * 更改进度提示
     * @param {number} percent
     */
    setProgress(percent) {
        this.front.scale.x = percent * 0.01;
        this.percent.setText(percent + '%');
    }

    /**
     * 更改进度条提示语
     * @param {string} text
     */
    setTips(text) {
        this.tips.setText(text);
    }

    destroy(destroyChildren, soft) {
        this.percent = null;
        this.front = null;
        this.back = null;
        this.tips = null;

        super.destroy(destroyChildren, soft);
    }
}

export default class Loading extends Base {
    /**
     * @type {ProgressBar} 进度提示
     */
    progressBar;

    /**
     * @type {Model.Loadinfo[]}
     */
    loadinfos;

    /**
     * @type {Phaser.BitmapData}
     */
    capturedScreen;

    onFileComplete = new Signal();

    isloaded = false;

    hasAudio = false;
    audios = [];

    init(loadinfos, capturedScreen) {
        super.init();

        this.progressBar = null;

        this.loadinfos = loadinfos;
        this.capturedScreen = capturedScreen;

        this.isloaded = false;
    }

    create() {
        let game = this.game;

        let capturedScreen = this.capturedScreen;
        if (capturedScreen) {
            let backgroundBitmapData = game.make.bitmapData(game.width, game.height);
            backgroundBitmapData.copy(capturedScreen, 0, 0, game.width, game.height, 0, 0, game.width, game.height);
            // this.backgroundBitmapData.copy(capturedScreen, 0, -game.height, game.width, 0, 0, 0, game.width, game.height, -90);

            let background = game.add.sprite(0, 0, backgroundBitmapData, null, this.baseGroup);
            // @todo 如果是手机横屏，则改变背景方向
            let msr = _.get(this.game, 'msr');
            if(msr && msr.isRotated()){
                background.y = this.game.width;
                background.rotation = Phaser.Math.degToRad(Phaser.Math.wrapAngle(-90));
            }
        }

        // 加载文件列表
        this.loadinfos.forEach((loadinfo) => {
            if (loadinfo.type === Model.Loadinfo.Type.IMAGE) {
                game.load.image(loadinfo.key, Url.file(loadinfo.filePath));
            }

            if (loadinfo.type === Model.Loadinfo.Type.AUDIO) {
                this.hasAudio = true;
                this.audios.push(loadinfo.key);
                game.load.audio(loadinfo.key, Url.file(loadinfo.filePath), true);
            }
        });

        this.progressBar = new ProgressBar(this.game, this.baseGroup);

        // 绑定加载事件
        game.load.onFileStart.add(this.fileStart, this);
        game.load.onFileComplete.add(this.fileComplete, this);
        game.load.onLoadComplete.add(this.loadComplete, this);

        game.load.start();
    }

    fileStart(progress, fileKey, filePath) {
        this.isloaded = false;
        this.progressBar.setTips('下载资源:  ' + filePath);
    }

    fileComplete(progress, cacheKey, success, totalLoaded, totalFiles) {
        if (!success) {
            console.log('Scene Loading: load %s is failed', cacheKey);
        }

        // every frame during loading, set the scale.x of the bar to the progress (an integer between 0
        // and 100) divided by 100 to give a float between 0 and 1
        this.progressBar.setProgress(progress);
        this.onFileComplete.dispatch(cacheKey, success);
        // console.log(progress + "% " + cacheKey + ' ' + totalLoaded + '/' + totalFiles);
    }

    loadComplete() {
        this.isloaded = true;

        this.game.load.onFileStart.remove(this.fileStart, this);
        this.game.load.onFileComplete.remove(this.fileComplete, this);
        this.game.load.onLoadComplete.remove(this.loadComplete, this);
    }

    checkAudioIsLoaded() {
        if (this.audios.length) {
            _.remove(this.audios, (audio) => {
                return this.game.cache.isSoundDecoded(audio);
            });
        } else {
            this.hasAudio = false;
        }
    }

    render() {

        // 如果加载完成，则进行外部通知
        if (this.isloaded) {

            if (this.hasAudio) {
                this.checkAudioIsLoaded();
                return;
            }

            if (this.onRenderFinish.getNumListeners() > 0) {
                console.log('Scene Loading: loaded');
                this.onRenderFinish.dispatch();
            }
        }
    }

    shutdown() {

        this.progressBar.destroy();
        this.progressBar = null;

        this.loadinfos = null;
        this.capturedScreen = null;

        super.shutdown();
    }
}



