// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Base from './base';
import * as Material from '../material';

export default class Name extends Base {
    /**
     * @type {Model.Element.Item.Name}
     */
    model;

    material;

    init() {
        let font = this.model.font ? this.model.font : Modeler.game.getFont();

        if (this.model.text) {
            let material = Modeler.material.find(this.model.materialId);
            console.assert(material instanceof Model.Material.Item, 'Render Element Name: 找不到对应素材 ' + this.model.materialId);
            /**
             * @type {Model.Material.Metainfo.TextBox}
             */
            let metainfo = _.clone(material.getMetainfo());
            metainfo.subs.text.text = this.model.text;

            this.material = new Material.TextBox(
                this.game,
                this,
                metainfo,
                font,
                'left'
            );
        }
    }

    destroy(destroyChildren, soft) {
        this.material = null;

        super.destroy(true, soft);
    }
}





