"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Collection = require("../base/collection");
const Item = require("./item");
class List extends Collection.List {
    constructor(list) {
        super();
        this.currentId = -1;
        this.nextId = -1;
        for (var i = 0; i < list.length; i++) {
            // var item = new Item(list[i]);
            var item = Item.factory.create(list[i]);
            this.list[i] = item;
        }
    }
    setCurrentId(currentId) {
        this.currentId = currentId;
        console.log('Model Scene List: set current ', this.currentId);
    }
    getIndex(id) {
        let item = this.find(id);
        if (item) {
            return this.list.indexOf(item);
        }
        else {
            return -1;
        }
    }
    getCurrent() {
        return this.find(this.currentId);
    }
    setNextId(id) {
        this.nextId = id;
        // console.log('Model Scene List: set next', id);
    }
    getNext() {
        let next;
        // 如果指定下一个，则直接取下一个
        if (this.nextId > -1) {
            next = this.find(this.nextId);
            console.assert(next, 'Model Scene List: 不能找到场景 ' + this.nextId);
            this.currentId = next.id;
            this.nextId = -1;
            return next;
        }
        // 还未加载过任何场景时
        if (this.currentId < 1) {
            next = this.find(1);
            console.assert(next, 'Model Scene List: 不能找到默认场景1');
            this.currentId = next ? next.id : -1;
            return next;
        }
        // 当前场景的下一个
        let current = this.getCurrent();
        let nextId;
        switch (current.type) {
            case Item.Type.NORMAL:
                // 这里不用考虑 option 的问题，由 renderer 处理
                nextId = current.next;
                console.assert(nextId, 'Model Scene List: 场景 ' + current.id + ' 不存在 next 属性');
                break;
            case Item.Type.OPERATION:
                nextId = current.next;
                break;
        }
        next = this.find(nextId);
        console.assert(next, 'Model Scene List: 不能找到当前场景的下个场景 ' + nextId);
        this.currentId = next ? next.id : -1;
        return next;
    }
}
exports.default = List;



