// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';

export default class Image extends Phaser.Image {
    /**
     *
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Material.Metainfo.Image} model
     * @param {boolean} [isLeftTop]
     */
    constructor(game, parent, model, isLeftTop) {
        super(game, model.x, model.y, model.loadinfo.key);
        parent.add(this);

        this.scale.x = model.w / this.width;
        this.scale.y = model.h / this.height;

        if (!isLeftTop) {
            // 修正以中心点为基准点
            this.anchor.setTo(0.5, 0.5);
            this.x = model.x + model.halfW();
            this.y = model.y + model.halfH();
        }

    }
}





