// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Base from './base';

export default class Snow extends Base {

    emitters = [];
    constructor(game) {
        super(game);
    }

    run() {
        this.start();
    }

    start() {
        /**
         * @type {Phaser.Game}
         */

        let game = this.game;

        let emitter1 = game.add.emitter(game.width / 2, 0, 600);
        let emitter2 = game.add.emitter(game.width / 2, game.height / 3, 60);
        let emitter3 = game.add.emitter(game.width / 2, game.height * 2 / 3, 60);

        emitter1.makeParticles('effect',['snow1.png']);
        emitter2.makeParticles('effect',['snow2.png']);
        emitter3.makeParticles('effect',['snow3.png']);

        let emitters = [emitter1, emitter2, emitter3];

        for (var i = 0; i < emitters.length; i++) {
            emitters[i].width = game.width;
            // emitters[i].angle = -28;
            if (i > 0) {
                emitters[i].area = new Phaser.Rectangle(0, 0, this.game.width, this.game.height);
            } else {
                emitters[i].area = new Phaser.Rectangle(0, -this.game.height / 2, this.game.width, this.game.height / 2);
            }

            emitters[i].gravity = 0;

            emitters[i].minParticleScale = 0.4;
            emitters[i].maxParticleScale = 0.8;

            emitters[i].setYSpeed(100, 160);
            emitters[i].setXSpeed(-5, 5);

            emitters[i].setAlpha(80, 100);

            emitters[i].minRotation = 0;
            emitters[i].maxRotation = 0;

            emitters[i].start(false, 12000, 100, 0);

        }

        this.emitters = emitters;
        this.timeEvent = game.time.events.add(Phaser.Timer.SECOND * 12, this.destroyRedundancyEmitters, this, this.emitters);

    }

    destroyRedundancyEmitters(emitters) {
        while (emitters.length > 1) {
            emitters[emitters.length - 1].destroy();
            emitters.pop();
        }
    }

    pause() {
        if (this.emitters.length > 0) {
            for (var i = 0; i < this.emitters.length; i++) {
                this.emitters[i].visible = false;
            }
        }
    }

    resume() {
        if (this.emitters.length > 0) {
            for (var i = 0; i < this.emitters.length; i++) {
                this.emitters[i].visible = true;
            }
        }
    }

    destroy() {
        if (this.game && this.timeEvent) {
            this.game.time.events.remove(this.timeEvent);
        }
        for (var i = 0; i < this.emitters.length; i++) {
            this.emitters[i].destroy();
        }

        //this.removeImage('snow');
        super.destroy();
    }
}



