// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Image from './image';

import ElementSwitcher from '../element/switcher';

export default class Switcher extends Phaser.Group {
    /**
     * @type {Model.Material.Metainfo.Switcher}
     */
    model;

    /**
     * @type {ElementSwitcher}
     */
    parent;

    isOn = false;

    /**
     *
     * @param {Phaser.Game} game
     * @param {ElementSwitcher} parent
     * @param {Model.Material.Metainfo.Switcher} model
     */
    constructor(game, parent, model) {
        super(game, parent, 'MaterialSwitcher' + parent.model.materialId);
        this.init(model);
    }

    /**
     *
     * @param {Model.Material.Metainfo.Switcher} model
     */
    init(model) {

        this.model = model;

        /**
         * @type {Model.Material.Metainfo.Switcher}
         */
        let metainfo = model;

        this.x = metainfo.x;
        this.y = metainfo.y;
        this.width = metainfo.w;
        this.height = metainfo.h;

        // 需要打开子元素的输入事件
        this.inputEnableChildren = true;

        this.offNormalImage = new Image(this.game, this, metainfo.subs.offNormal);
        this.offHoverImage = new Image(this.game, this, metainfo.subs.offHover);
        this.onNormalImage = new Image(this.game, this, metainfo.subs.onNormal);
        this.onHoverImage = new Image(this.game, this, metainfo.subs.onHover);

        this.reset();

        if (this.parent.model.interaction) {
            let interaction = this.parent.model.interaction;
            Interactor.configure(interaction);

            this.isOn = interaction.params.get('isOn');
        }
        // 默认显示效果 = 鼠标移出效果
        this.onChildInputOutHandler();
    }

    reset() {
        this.offHoverImage.visible = false;
        this.offNormalImage.visible = false;
        this.onHoverImage.visible = false;
        this.onNormalImage.visible = false;
    }

    changeState() {
        this.isOn = !this.isOn;
        if (this.parent.model.interaction) {
            let interaction = this.parent.model.interaction;
            interaction.params.set('isOn', this.isOn);
            Interactor.trigger(interaction);
        }
    }

    bindInteraction() {
        this.onChildInputDown.add(this.onChildInputDownHandler, this);
        this.onChildInputOver.add(this.onChildInputOverHandler, this);
        this.onChildInputOut.add(this.onChildInputOutHandler, this);
    }

    unbindInteraction() {
        this.onChildInputDown.remove(this.onChildInputDownHandler, this);
        this.onChildInputOver.remove(this.onChildInputOverHandler, this);
        this.onChildInputOut.remove(this.onChildInputOutHandler, this);
    }

    onChildInputDownHandler(sprite, pointer) {
        //console.log('button id ' + this.data.id + ' clicked');
        this.changeState();
        // 改变下显示的内容
        this.onChildInputOverHandler();
    }

    onChildInputOverHandler(sprite, pointer) {
        this.reset();
        if (this.isOn) {
            this.onHoverImage.visible = true;
        } else {
            this.offHoverImage.visible = true;
        }
    }

    onChildInputOutHandler(sprite, pointer) {
        this.reset();
        if (this.isOn) {
            this.onNormalImage.visible = true;
        } else {
            this.offNormalImage.visible = true;
        }
    }

    destroy(destroyChildren, soft) {
        this.unbindInteraction();

        this.offNormalImage = null;
        this.frontImage = null;
        this.onNormalImage = null;
        this.onHoverImage = null;

        super.destroy(true, soft);
    }
}





