// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';


import Interactor from './interactor';

import * as Model from '../model/';
import Modeler from './modeler';

import Scene from '../render/scene';

import ElementAnimator from '../animator/element';
import SceneAnimator from '../animator/scene';
import EffectAnimator from '../animator/effect';


export default class Animator {
    /**
     * @type {Phaser.Game}
     */
    static game;

    static elementAnimator = ElementAnimator;
    static sceneAnimator = SceneAnimator;
    static effectAnimator = EffectAnimator;

    /**
     * @type {Scene.Normal}
     */
    static scene = null;

    static reset() {
        this.sceneAnimator.reset();
        this.elementAnimator.reset();
        this.effectAnimator.reset();
    }

    /**
     *
     * @param {Scene.Normal} scene
     */
    static setScene(scene) {
        this.scene = scene;
        this.game = scene.game;
        this.sceneAnimator.setScene(scene);
        this.elementAnimator.setScene(scene);
        this.effectAnimator.setScene(scene);
        this.reset();
    }


    static speedUp() {
        // console.log('Animator: 加速');
        this.isSpeedUp = true;
        if(this.elementAnimator.hasAnimation()){
            this.elementAnimator.speedUp();
        }
        if(this.sceneAnimator.hasAnimation()){
            this.sceneAnimator.speedUp();
        }

    }

    static speedNormal() {
        // console.log('Animator: 正常速度');
        this.isSpeedUp = false;
        if(this.elementAnimator.hasAnimation()){
            this.elementAnimator.speedNormal();
        }
        if(this.sceneAnimator.hasAnimation()){
            this.sceneAnimator.speedNormal();
        }
    }

    static start() {
        this.scene.event.start();
        Interactor.addTarget(this);
        this.sceneAnimator.onComplete.addOnce(this.onSceneAnimatorCompleteHandle, this);
        this.sceneAnimator.start();
        this.effectAnimator.start();
    }

    static onSceneAnimatorCompleteHandle() {
        this.elementAnimator.onComplete.addOnce(this.onCompleteHandle, this);
        this.elementAnimator.startTypewriter();
        this.elementAnimator.startAnimation();
    }

    static update() {
        this.elementAnimator.update();
        // this.effectAnimator.update();
    }

    static onCompleteHandle() {
        Interactor.removeTarget(this);
        // 通知动画结束
        this.scene.event.complate();
    }

    static pause() {
        this.sceneAnimator.pause();
        this.elementAnimator.pause();
        this.effectAnimator.pause();
    }

    static resume() {
        this.sceneAnimator.resume();
        this.elementAnimator.resume();
        this.effectAnimator.resume();
    }


    static bindInteraction() {
        this.elementAnimator.bindInteraction();
    }
    static unbindInteraction() {
        this.elementAnimator.unbindInteraction();
    }

    static shutdown() {
        this.effectAnimator.shutdown();
    }

}



