// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Base from './base';
import * as Material from '../material';

export default class Button extends Base {
    /**
     * @type {Model.Element.Item.Button}
     */
    model;

    material;

    init() {

        let font = Modeler.game.getFont();
        /**
         * @type {Model.Material.Metainfo.Button}
         */
        let metainfo = Modeler.material.find(this.model.materialId).getMetainfo();

        // button文本内容为空
        metainfo.subs.text.text = '';

        this.material = new Material.Button(this.game, this, metainfo, font);

        Interactor.addTarget(this.material);
    }

    destroy(destroyChildren, soft) {
        Interactor.removeTarget(this.material);
        this.material = null;
        super.destroy(true, soft);
    }
}





