// @ts-check
import * as _ from 'lodash';

import { Phaser, PIXI } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';
import Url from '../../utils/url';

import Image from './image';
import Text from './text';
import Scrollable from './scrollable';

import ElementSaved from '../element/saved';

/**
 * @extends Phaser.Group
 */
class SavedItem extends Phaser.Group {
    /**
     * @type {Model.Material.Metainfo.Saved}
     */
    model;

    /**
     * @type {SavedList}
     */
    parent;

    index;

    /**
     *
     * @param {Phaser.Game} game
     * @param {SavedList} parent
     * @param {Model.Material.Metainfo.Saved} model
     * @parma {{name:string, size:number, color:number}} font
     */
    constructor(game, parent, model, font, index) {
        super(game, parent, 'MaterialSavedItem');
        this.init(model, font);
        // this.parent = parent;
        this.index = index;
    }

    /**
     *
     * @param {Model.Material.Metainfo.Saved} model
     * @parma {{name:string, size:number, color:number}} font
     */
    init(model, font) {

        /**
         * @type {Model.Material.Metainfo.Saved}
         */
        let metainfo = model;

        this.x = metainfo.x;
        this.y = metainfo.y;
        this.width = metainfo.w;
        this.height = metainfo.h;

        // 需要打开子元素的输入事件
        this.inputEnableChildren = true;

        this.backgroundNormal = new Image(this.game, this, metainfo.subs.backgroundNormal);
        this.backgroundHover = new Image(this.game, this, metainfo.subs.backgroundHover);
        this.backgroundHover.visible = false;

        // 处理截图
        this.thumbnail = new Image(this.game, this, metainfo.subs.thumbnail, true);
        /**
         * @type {Model.Game.Saved.Item}
         */
        let gameSaved = metainfo.props.get('gameSaved');
        if (!gameSaved.isNull()) {


            // 处理时间戳
            var dateText = metainfo.subs.date;
            if (dateText) {
                var time = new Date(gameSaved.get(Model.Game.Saved.Item.Key.time));
                dateText.text = time.getFullYear() + "/" + this.formatTime(time.getMonth() + 1) + "/" + this.formatTime(time.getDate()) + "  " + this.formatTime(time.getHours()) + ":" + this.formatTime(time.getMinutes());

                new Text(this.game, this, dateText, font);
            }

            // 场景信息
            var sectionNameText = metainfo.subs.sectionName;
            if (sectionNameText) {
                // @todo 场景改名
                sectionNameText.text = gameSaved.get(Model.Game.Saved.Item.Key.name);

                new Text(this.game, this, sectionNameText, font);
            }
        }
    }

    formatTime(n) {
        if (n < 10) {
            return '0' + n;
        } else {
            return n;
        }
    }

    bindInteraction() {
        this.onChildInputOver.add(this.onChildInputOverHandler, this);
        this.onChildInputOut.add(this.onChildInputOutHandler, this);
        this.onChildInputDown.add(this.onChildInputDownHandler, this);
    }

    unbindInteraction() {
        this.onChildInputOver.remove(this.onChildInputOverHandler, this);
        this.onChildInputOut.remove(this.onChildInputOutHandler, this);
        this.onChildInputDown.remove(this.onChildInputDownHandler, this);
    }

    onChildInputOverHandler(sprite, pointer) {
        this.backgroundNormal.visible = false;
        this.backgroundHover.visible = true;
    }

    onChildInputOutHandler(sprite, pointer) {
        this.backgroundNormal.visible = true;
        this.backgroundHover.visible = false;
    }

    onChildInputDownHandler(sprite, pointer) {

        // 元素回到初始状态
        this.backgroundNormal.visible = true;
        this.backgroundHover.visible = false;
        let interaction = this.parent.parent.model.interaction;
        interaction.params.set('index', this.index);
        Interactor.trigger(interaction);
    }

    destroy(destroyChildren, soft) {
        this.unbindInteraction();

        this.backgroundNormal = null;
        this.backgroundNormal = null;

        super.destroy(true, soft);
    }
}

class ThumbnailLoader {
    /**
     * @type {Phaser.Game}
     */
    game;

    /**
     * @type {[string, SavedItem][]}
     */
    loadBuffers = [];

    constructor(game) {
        this.game = game;
    }


    add(thumbnail, savedItem) {
        if (this.game.load.checkKeyExists('image', thumbnail)) {
            this.change(thumbnail, savedItem);
        } else {
            this.loadBuffers.push([thumbnail, savedItem]);
        }
    }

    start() {
        let game = this.game;
        if (this.loadBuffers.length) {
            // 绑定加载事件
            game.load.onFileStart.add(this.fileStart, this);
            game.load.onFileComplete.add(this.fileComplete, this);
            game.load.onLoadComplete.add(this.loadComplete, this);

            _.forEach(this.loadBuffers, (buffer) => {
                game.load.image(buffer[0], Url.file(buffer[0]));
            });

            game.load.start();
        }
    }

    fileStart(progress, fileKey, filePath) {
    }

    fileComplete(progress, cacheKey, success, totalLoaded, totalFiles) {
        // console.log(progress + "% " + cacheKey + ' ' + totalLoaded + '/' + totalFiles);
        if (!success) {
            return;
        }
        _.remove(this.loadBuffers, (buffer) => {
            if(buffer[0] === cacheKey) {
                this.change(buffer[0], buffer[1]);
                return true;
            } else {
                return false;
            }
        });
    }

    loadComplete() {
        this.loadBuffers = [];

        this.game.load.onFileStart.remove(this.fileStart, this);
        this.game.load.onFileComplete.remove(this.fileComplete, this);
        this.game.load.onLoadComplete.remove(this.loadComplete, this);
    }

    /**
     *
     * @param {string} thumbnail
     * @param {SavedItem} savedItem
     */
    change(thumbnail, savedItem) {

        // 解决图片大小和 savedItem.thumbnail 区域大小不一致
        let image = this.game.make.sprite(0, 0, thumbnail);
        image.scale.x = savedItem.thumbnail.width / image.width;
        image.scale.y = savedItem.thumbnail.height / image.height;
        savedItem.thumbnail.addChild(image);

        // 如果大小一致的话，可以直接使用这句
        // savedItem.thumbnail.loadTexture(thumbnail);

    }

    destroy() {
        this.loadBuffers = [];
        this.loadComplete();
    }
}

export default class SavedList extends Scrollable {
/**
     * @type {Model.Material.Metainfo.Saved}
     */
    model;

    /**
     * @type {ElementSaved}
     */
    parent;

    /**
     * @type {ThumbnailLoader}
     */
    thumbnailLoader;

    /**
     *
     * @param {Phaser.Game} game
     * @param {ElementSaved} parent
     * @param {Model.Material.Metainfo.Saved} model
     * @parma {{name:string, size:number, color:number}} font
     */
    constructor(game, parent, model, font) {
        super(game, parent, 'MaterialSavedList');
        this.thumbnailLoader = new ThumbnailLoader(game);
        this.init(model, font);
    }

    /**
     *
     * @param {Model.Material.Metainfo.Saved} model
     * @parma {{name:string, size:number, color:number}} font
     * @parma {string} text
     */
    init(model, font) {

        let ParentModel = this.parent.model;
        /**
         * @type {Model.Material.Metainfo.Saved}
         */
        let metainfo = model;

        this.x = 0;
        this.y = 0;

        this.setMaskRect(0, 0, ParentModel.w, ParentModel.h);

        // ParentModel.total = 8;
        for (let i = 0; i < ParentModel.total; i++) {
            // 获取数据
            let gameSaved = Modeler.game.saved.load(i);

            // 偏移计算公式
            // x = 0 * 间隔（模n为0） or 1 * 间隔（模n为1）or ...
            // y = 0 * 间隔（n的0倍）or 1 * 间隔（n的1倍）or ...
            let offsetX = 0 + (i % ParentModel.cols) * (metainfo.w + ParentModel.margin);
            let offsetY = 0 + Math.floor(i / ParentModel.cols) * (metainfo.h + ParentModel.margin);

            let _metainfo = _.clone(metainfo);
            _metainfo.props.set('gameSaved', gameSaved);
            _metainfo.x += offsetX;
            _metainfo.y += offsetY;
            let material = new SavedItem(this.game, this, _metainfo, font, i);
            this.material = material;
            Interactor.addTarget(material);

            let thumbnail = gameSaved.get(Model.Game.Saved.Item.Key.thumbnail);
            if (thumbnail) {
                this.thumbnailLoader.add(thumbnail, material);
            }
        }

        this.thumbnailLoader.start();
    }

    destroy(destroyChildren, soft) {
        Interactor.removeTarget(this.material);

        // this.scrollable = null;

        this.thumbnailLoader.destroy();

        super.destroy(true, soft);
    }
}




