"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Struct = require("../../lhotse/src/struct");
const Utils = require("../../utils");
const game_1 = require("../../game");
const base_1 = require("./base");
const SelfUtils = require("./utils");
class Option {
    constructor(data) {
        this.id = -1;
        this.variableId = -1;
        this.next = -1;
        this.id = Utils.getInt(data, 'id', this.id);
        this.variableId = Utils.getInt(data, 'variableId', this.variableId);
        this.operator = Utils.getInt(data, 'operator', this.operator);
        this.target = new SelfUtils.Target(data.target);
        this.next = Utils.getInt(data, 'next', this.next);
    }
    check() {
        var variable = game_1.default.variable.find(this.variableId);
        // 找不到变量，不应用数值检测
        if (!variable) {
            console.log("Model Scene Condition: 用户 variables " + this.variableId + "找不到");
            return;
        }
        let operator = '';
        let value = this.target.getValue();
        let result;
        switch (this.operator) {
            case Struct.Scene.Condition.Operator.EQUAL:
                operator = '==';
                result = variable.get() === value;
                break;
            case Struct.Scene.Condition.Operator.LESS:
                operator = '<';
                result = variable.get() < value;
                break;
            case Struct.Scene.Condition.Operator.LESSEQUAL:
                operator = '<=';
                result = variable.get() <= value;
                break;
            case Struct.Scene.Condition.Operator.MORE:
                operator = '>';
                result = variable.get() > value;
                break;
            case Struct.Scene.Condition.Operator.MOREEQUAL:
                operator = '>=';
                result = variable.get() >= value;
                break;
            case Struct.Scene.Condition.Operator.NOTEQUAL:
                operator = '!=';
                result = variable.get() != value;
                break;
        }
        console.log(`Model Scene Condition: 数值检测 ${variable.desc}(V${variable.id}) 对比 ${this.target.toString()} ：`
            + `${variable.get()} ${operator} ${value} 结果 ${result}`);
        return result;
    }
}
class Condition extends base_1.default {
    constructor(data) {
        super(data);
        this.next = -1;
        this.options = [];
        this.next = Utils.getInt(data, 'next', this.next);
        if (_.has(data, 'options')) {
            _.forEach(data.options, (optionData) => {
                this.options.push(new Option(optionData));
            });
        }
    }
    run() {
        let next = this.next;
        let isFoundOption = false;
        _.forEach(this.options, (option) => {
            if (option.check()) {
                next = option.next !== -1 ? option.next : this.next;
                isFoundOption = true;
                return false;
            }
        });
        console.assert(isFoundOption, `Model Scene Condition: 条件场景 ${this.id} 所有条件都不成立`);
        console.assert(next !== -1, `Model Scene Condition: 条件场景 ${this.id} 没有 next`);
        return next;
    }
}
exports.default = Condition;



