"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Collection = require("../base/collection");
const Utils = require("../utils");
const Metainfo = require("./metainfo");
class Item extends Collection.Item {
    constructor(data) {
        super();
        this.id = '';
        this.type = '';
        this.metainfo = null;
        this.id = Utils.getString(data, 'id', this.id);
        this.type = Utils.getString(data, 'id', this.type);
        if (_.has(data, 'metainfo')) {
            this.metainfo = Metainfo.factory.create(data.metainfo);
        }
    }
    getLoadinfos() {
        let loadinfos = [];
        if (this.metainfo) {
            loadinfos = this.metainfo.getLoadinfos();
        }
        return loadinfos;
    }
    /**
     * 为了可以进行正常提示
     */
    getMetainfo() {
        return this.metainfo;
    }
}
exports.default = Item;



