// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Image from './image';
import Text from './text';

import ElementSlider from '../element/slider';

export default class Slider extends Phaser.Group {
    /**
     * @type {Model.Material.Metainfo.Slider}
     */
    model;

    /**
     * @type {ElementSlider}
     */
    parent;

    isSliding = false;

    /**
     *
     * @param {Phaser.Game} game
     * @param {ElementSlider} parent
     * @param {Model.Material.Metainfo.Slider} model
     */
    constructor(game, parent, model) {
        super(game, parent, 'MaterialSlider' + parent.model.materialId);
        this.init(model);
    }

    /**
     *
     * @param {Model.Material.Metainfo.Slider} model
     */
    init(model) {

        this.model = model;

        /**
         * @type {Model.Material.Metainfo.Slider}
         */
        let metainfo = model;

        this.x = metainfo.x;
        this.y = metainfo.y;
        this.width = metainfo.w;
        this.height = metainfo.h;

        // 需要打开子元素的输入事件
        this.inputEnableChildren = true;

        this.backImage = new Image(this.game, this, metainfo.subs.back, true);
        // this.backImage.y = (this.model.h - metainfo.subs.back.h) / 2;
        this.frontImage = new Image(this.game, this, metainfo.subs.front, true);
        // this.frontImage.y = (this.model.h - metainfo.subs.front.h) / 2;
        this.frontImageCropRect = new Phaser.Rectangle(
            0,
            0,
            metainfo.subs.front.w,
            metainfo.subs.front.h
        );

        this.pointerNormalImage = new Image(this.game, this, metainfo.subs.pointerNormal, true);
        this.pointerHoverImage = new Image(this.game, this, metainfo.subs.pointerHover, true);
        this.pointerHoverImage.visible = false;

        if (this.parent.model.interaction) {
            let interaction = this.parent.model.interaction;
            // @todo 初始化参数获取
            Interactor.configure(interaction);
            let progress = Math.max(0, Math.min(100, interaction.params.get('value')));
            this.setProgress(progress);
        }
    }

    setProgress(progress) {
        //console.log(progress);
        this.progress = progress;

        let progressSize = Math.floor(this.progress * 0.01 * this.model.w);

        this.frontImageCropRect.width = this.progress * 0.01 * this.model.subs.front.w;
        this.frontImage.crop(this.frontImageCropRect, false);

        // @ts-ignore
        let pointerHalfwidth = this.pointerNormalImage.getBounds().halfWidth;
        let x = Math.max(pointerHalfwidth, Math.min(this.model.w - pointerHalfwidth, progressSize));
        this.pointerNormalImage.x = x - pointerHalfwidth;
        this.pointerHoverImage.x = x - pointerHalfwidth;

    }

    callSetProgress(progress) {
        if (this.parent.model.interaction) {
            let interaction = this.parent.model.interaction;
            interaction.params.set('value', progress);
            Interactor.trigger(interaction);
        }
    }

    bindInteraction() {
        this.game.input.addMoveCallback(this.onMoveHandler, this);
        this.onChildInputDown.add(this.onChildInputDownHandler, this);
        this.onChildInputUp.add(this.onChildInputUpHandler, this);
        this.onChildInputOver.add(this.onChildInputOverHandler, this);
        this.onChildInputOut.add(this.onChildInputOutHandler, this);
    }

    unbindInteraction() {
        this.game.input.deleteMoveCallback(this.onMoveHandler, this);
        this.onChildInputDown.remove(this.onChildInputDownHandler, this);
        this.onChildInputUp.remove(this.onChildInputUpHandler, this);
        this.onChildInputOver.remove(this.onChildInputOverHandler, this);
        this.onChildInputOut.remove(this.onChildInputOutHandler, this);
    }

    /**
     *
     * @param {Phaser.Pointer} pointer
     */
    onMoveHandler(pointer) {
        //console.log('move');
        if (this.isSliding) {
            let x = pointer.x - this.worldPosition.x;
            x = Math.max(0, Math.min(this.model.w, x));

            let progress = Math.round((x / this.model.w) * 100);

            this.setProgress(progress);
            this.callSetProgress(progress);
        }
    }

    onChildInputDownHandler(sprite, pointer) {
        //console.log('down');
        this.isSliding = true;
        this.onMoveHandler(pointer);
    }

    onChildInputUpHandler(sprite, pointer) {
        //console.log('up');
        this.isSliding = false;
    }

    onChildInputOverHandler(sprite, pointer) {
        this.pointerNormalImage.visible = false;
        this.pointerHoverImage.visible = true;
    }

    onChildInputOutHandler(sprite, pointer) {
        this.pointerNormalImage.visible = true;
        this.pointerHoverImage.visible = false;
    }

    destroy(destroyChildren, soft) {
        this.unbindInteraction();

        this.backImage = null;
        this.frontImage = null;
        this.pointerNormalImage = null;
        this.pointerHoverImage = null;

        super.destroy(true, soft);
    }
}





