"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const _ = require("lodash");
const Utils = require("../utils");
const Saved = require("./saved");
exports.Saved = Saved;
const Variable = require("./variable");
exports.Variable = Variable;
const font_1 = require("./font");
exports.Font = font_1.default;
const story_1 = require("../story");
const Scene = require("../scene/index");
class Game {
    static formData(data) {
        if (_.has(data, 'info')) {
            Game.info = _.merge(Game.info, data.info);
        }
        if (_.has(data, 'setting')) {
            Game.setting = _.merge(Game.setting, data.setting);
        }
        this.variable = new Variable.List(Utils.getList(data, 'variables', []));
    }
    static setId(id) {
        this.id = id;
        Saved.Storagemanager.init(id);
    }
    static getFont() {
        return new font_1.default({
            family: 'font',
            size: this.setting.font.size,
            color: this.setting.font.color,
        });
    }
    static reset() {
        this.variable.reset();
        // @todo 是否要修改游戏其他数据，例如自动播放等
    }
    static save(index) {
        console.log('Model Game: 保存游戏 ' + index);
        let saved = this.makeSaved();
        this.saved.save(index, saved);
    }
    static load(index) {
        console.log('Model Game: 读取游戏 ' + index);
        let saved = this.saved.load(index);
        return this.applySaved(saved);
    }
    // 制作存档信息
    static makeSaved() {
        let saved = new Saved.Item();
        let section = story_1.default.section.getCurrent();
        console.assert(section, 'Model Game: makeSaved 当前小节不可读取');
        // 保存小节id
        saved.set(Saved.Item.Key.sectionId, section.id);
        let scene = section.scene.getCurrent();
        console.assert(scene instanceof Scene.Item.Normal, 'Model Game: makeSaved 当前场景不是可渲染场景');
        // 保存场景id
        saved.set(Saved.Item.Key.sceneId, scene.id);
        // 保存小节名
        saved.set(Saved.Item.Key.name, `${section.id} - ${scene.id}`);
        // 保存场景缩略图地址
        saved.set(Saved.Item.Key.thumbnail, scene.thumbnail);
        // 保存游戏数值
        saved.set(Saved.Item.Key.gameVariables, this.variable.toSaved());
        // 保存当前时间
        saved.set(Saved.Item.Key.time, +(new Date()));
        return saved;
    }
    static applySaved(saved) {
        if (saved.isNull()) {
            console.log("Model Game: applySaved 存档为空");
            return false;
        }
        if (!saved.isUsableVersion()) {
            console.log("Model Game: applySaved 存档不可用");
            return false;
        }
        // 恢复小节id
        story_1.default.section.setCurrentId(saved.get(Saved.Item.Key.sectionId));
        // 设置下一个场景
        let section = story_1.default.section.getCurrent();
        console.assert(section, 'Model Game: applySaved 当前小节不可读取');
        section.scene.setNextId(saved.get(Saved.Item.Key.sceneId));
        console.log('Model Game: set next', saved.get(Saved.Item.Key.sceneId));
        // 剧情重置
        story_1.default.playback.reset();
        // 恢复保存游戏数值
        this.variable.fromSaved(saved.get(Saved.Item.Key.gameVariables));
        return true;
    }
}
Game.info = {
    name: '',
    identifer: '',
    rendererVersion: '',
};
Game.setting = {
    screen: {
        width: 1024,
        height: 768,
        isPortrait: false,
        isFull: false,
    },
    font: {
        indexPath: '',
        picturePath: '',
        family: 'font',
        size: 29,
        color: '#ffffff',
    },
    isAutoPlay: false,
    audio: {
        effectVolume: 100,
        bgmVolume: 100,
        voiceVolume: 100,
    }
};
// 是否加速
Game.isSeepdUp = false;
Game.Saved = Saved;
Game.saved = new Saved.List;
exports.default = Game;



