/*global Engine {aaa}*/



export default class Url {

    /**
     * @typedef Options
     * @type {object}
     * @property {string} prefix - url前缀
     * @property {string} bust - 消除缓存
     * @property {string} systemPrefix - 系统url前缀
     */

    /**
     * @type {Options}
     */

    static options = {
        prefix: '',
        bust: '',
        systemPrefix: '',
    };

    static file(filepath) {
        if (this.options.gameUpdateTime) {
            filepath += '?bust=' + this.options.gameUpdateTime;
        }
        return filepath;
    }

    static systemFile(filepath){
        filepath = this.options.systemPrefix + filepath;
        console.log(this.options.systemPrefix)
        return filepath;
    }
}



