// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Modeler from '../../core/modeler';
import Interactor from '../../core/interactor';

import Base from './base';
import * as Material from '../material';

export default class Dialogue extends Base {
    /**
     * @type {Model.Element.Item.Dialogue}
     */
    model;

    /**
     * @type {Material.TextBox}
     */
    material;

    disChildrenCount = 0;
    disLineCount = 0;

    maxLine = 3;
    maxLineLength = 100;

    init() {
        let font = this.model.font ? this.model.font : Modeler.game.getFont();
        if (this.model.text) {
            let material = Modeler.material.find(this.model.materialId);
            console.assert(material instanceof Model.Material.Item, 'Render Element Dialogue: 找不到对应素材 ' + this.model.materialId);
            /**
             * @type {Model.Material.Metainfo.TextBox}
             */
            let metainfo = _.clone(material.getMetainfo());
            metainfo.subs.text.text = this.model.text;

            this.material = new Material.TextBox(
                this.game,
                this,
                metainfo,
                font,
                'left'
            );

            // 清空文字内容
            this.material.renderText.setText('');
        }
        // Interactor.addTarget(this.material);
    }

    get length() {
        return this.material.renderText.text.length;
    }

    set length(length) {
        this.showTextByLength(length);
    }

    showTextByLength(length) {
        this.disChildrenCount = 0;

        let text = this.model.text;

        //若已显示完毕，则直接返回
        if (length === this.material.renderText.text.length) {
            return;
        }

        let texts = text.substring(0, length + 1).split("\n");
        var copyOfTextArr = [];
        for (var i = 0; i < texts.length; i++) {
            copyOfTextArr[i] = texts[i];
        }
        //先把已经被顶上去的行去除，避免重复往上顶
        if (this.disLineCount > 0) {
            texts.splice(0, this.disLineCount);
        }

        //若文字行数大于最大行数则往上顶
        if (texts.length > this.maxLine) {
            var disLine = texts.length - this.maxLine;
            //往上顶disLine行
            texts.splice(0, disLine);
            //被顶上去的行数+disLine
            this.disLineCount += disLine;
            //换行时需要清除缓存
            this.material.renderText.clearGlyphs();
        }

        for (let i = 0; i < this.disLineCount; i++) {
            this.disChildrenCount += copyOfTextArr[i].length;
        }

        //若单行文字超过最大长度则将其截掉
        for (var j = 0; j < texts.length; j++) {
            if (texts[j].length > this.maxLineLength) {
                texts[j] = texts[j].substring(0, this.maxLineLength);
            }
        }

        this.material.renderText.text = texts.join("\n");
    }

    destroy(destroyChildren, soft) {
        this.material = null;

        super.destroy(true, soft);
    }
}





