// @ts-check
import { Phaser } from '../../utils/load-phaser';

import Base from './base';

import Modeler from '../../core/modeler';
import * as Model from '../../model';
import Interactor from '../../core/interactor';
import Animator from '../../core/animator';
import AudioPlayer from '../../core/audioplayer';

import Url from '../../utils/url';

import * as Element from '../element';

export default class Normal extends Base {
    /**
     * @type {Model.Scene.Item.Normal}
     */
    model;

    waitUserInput = false;
    isBindCancelAutoPlay = false;

    init(model) {
        super.init();

        this.model = model;

        this.waitUserInput = false;
        this.isBindCancelAutoPlay = false;

        Interactor.addTarget(this);
    }

    create() {
        this.baseGroup.inputEnableChildren = true;

        // 添加一个可点击的空白精灵，实现触发点击空白效果
        this.baseClickSprite = this.game.add.sprite(0, 0);
        this.baseClickSprite.width = this.game.width;
        this.baseClickSprite.height = this.game.height;
        this.baseGroup.add(this.baseClickSprite);

        if (this.model.hasElement()) {
            for (let i = 0; i < this.model.element.list.length; i++) {
                let element = this.model.element.list[i];

                for (var j = 0; j < this.model.animation.list.length; j++) {
                    let tempAnimation = this.model.animation.list[j];
                    if (tempAnimation.target.id == element.id) {
                        //将from动画统一转化为to动画
                        tempAnimation.swapFrom(element);
                        if (this.model.animation.list[j + 1]) {
                            if (this.model.animation.list[j + 1].target.id != element.id || this.model.animation.list[j + 1].trigger.type != 0) {
                                break;
                            }
                        }
                    }

                }

                Element.factory.create(this.game, this.baseGroup, element);
            }
        }

        this.createAnimation();
        this.createAudio();

        // 没有过渡动画，则直接调用过渡后的内容
        if (!this.model.hasTransition()) {
            this.postTransit();
        }
    }

    createAnimation() {
        Animator.setScene(this);
        let hasTypewriterAnimation = false;
        if (this.model.hasAnimation()) {
            for (let i = 0; i < this.model.animation.list.length; i++) {
                let animation = this.model.animation.list[i];
                if (!animation.isTypewriter()) {
                    Animator.elementAnimator.addAnimation(animation);
                } else {
                    hasTypewriterAnimation = true;
                    Animator.elementAnimator.addTypewriter(animation);
                }

            }
        }
        //如果没有打字机动画，则将其标志改为完成
        if (!hasTypewriterAnimation) {
            Animator.elementAnimator.changeTypewriterAnimationFlag();
        }
    }

    createAudio() {
        AudioPlayer.setScene(this);
        if (this.model.audio.bgm) {
            AudioPlayer.playBgm(this.model.audio.bgm);
        }

        if (this.model.hasAudio()) {

            for (let i = 0; i < this.model.audio.list.length; i++) {
                /**
                 * @type {*}
                 */
                let audio = this.model.audio.list[i];
                AudioPlayer.add(audio);
            }

            AudioPlayer.start();
        }
    }

    /**
     * 此方法名为固定名称，过渡动画用
     */
    postTransit() {
        Animator.start();

        if (this.model.hasAudio()) {
            //AudioPlayer.start();
        }
    }

    update() {
        Animator.update();
        AudioPlayer.update();
    }

    render() {

        // 有选项不考虑事件执行情况
        if (this.model.hasOption) {
            return;
        }
        // 事件如果没有执行完毕，则继续执行事件
        if (!this.event.isFinish()) {
            return;
        }

        // 因为不知道何时按下快捷键，所以放在这里监听
        if (Modeler.game.setting.isAutoPlay && !this.isBindCancelAutoPlay) {
            this.isBindCancelAutoPlay = true;
            this.baseGroup.onChildInputDown.addOnce(this.onCancelAutoPlayHandle, this);
        }
        if (!Modeler.game.setting.isAutoPlay && this.isBindCancelAutoPlay) {
            this.isBindCancelAutoPlay = false;
            this.baseGroup.onChildInputDown.remove(this.onCancelAutoPlayHandle, this);
        }

        if (Modeler.game.setting.isAutoPlay) {
            this.jumpNextScene();
        }

        // 已经绑定过了单击切换下一个场景，就等待用户输入
        if (this.waitUserInput) {
            return;
        }

        this.baseGroup.onChildInputDown.addOnce(this.onClickHandle, this);
        this.waitUserInput = true;
    }

    onClickHandle() {
        this.jumpNextScene();
    }

    onCancelAutoPlayHandle() {
        //判断按下的是鼠标左键且自动播放
        if (arguments[1].leftButton.isDown && Modeler.game.setting.isAutoPlay) {
            Modeler.game.setting.isAutoPlay = false;
        }
    }

    jumpNextScene() {
        let interaction = new Model.Interaction({ id: Model.Interaction.Id.JUMP_NEXT_SCENE });
        Interactor.trigger(interaction);
    }

    bindInteraction() {

        // 键盘快捷键是否放到Interactor中？
        this.game.input.keyboard.addKey(Phaser.Keyboard.Z).onDown.add(function () {
            Modeler.game.isSeepdUp = true;
            Animator.speedUp();
        });
        this.game.input.keyboard.addKey(Phaser.Keyboard.Z).onUp.add(function () {
            Modeler.game.isSeepdUp = false;
            Animator.speedNormal();
        });
        this.game.input.keyboard.addKey(Phaser.Keyboard.X).onDown.add(() => {
            let interaction = new Model.Interaction({
                id: Model.Interaction.Id.SET_AUTO_PLAY
            });
            interaction.params.set('isOn', !Modeler.game.setting.isAutoPlay);
            Interactor.trigger(interaction);
        });

        if (this.isBindCancelAutoPlay) {
            this.baseGroup.onChildInputDown.addOnce(this.onCancelAutoPlayHandle, this);
        }
        if (this.waitUserInput) {
            this.baseGroup.onChildInputDown.addOnce(this.onClickHandle, this);
        }
    }

    unbindInteraction() {
        this.game.input.keyboard.removeKey(Phaser.Keyboard.Z);
        this.game.input.keyboard.removeKey(Phaser.Keyboard.X);

        if (this.isBindCancelAutoPlay) {
            this.baseGroup.onChildInputDown.remove(this.onCancelAutoPlayHandle, this);
        }
        if (this.waitUserInput) {
            this.baseGroup.onChildInputDown.remove(this.onClickHandle, this);
        }
    }

    shutdown() {
        this.model = null;
        Interactor.removeTarget(this);

        Animator.shutdown();

        super.shutdown();
    }
}



