"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
const Struct = require("../../lhotse/src/struct");
const Utils = require("../../utils");
const game_1 = require("../../game");
class Target {
    constructor(data) {
        this.type = Struct.Scene.Operation.Target.Type.NUMBER;
        this.value = 0;
        this.begin = 0;
        this.end = 0;
        this.variableId = -1;
        this.type = Utils.getInt(data, 'type', this.type);
        if (this.type === Struct.Scene.Operation.Target.Type.NUMBER) {
            this.value = Utils.getInt(data, 'value', this.value);
        }
        if (this.type === Struct.Scene.Operation.Target.Type.RANDOM) {
            this.begin = Utils.getInt(data, 'begin', this.begin);
            this.end = Utils.getInt(data, 'end', this.end);
        }
        if (this.type === Struct.Scene.Operation.Target.Type.VARIABLE) {
            this.variableId = Utils.getInt(data, 'variableId', this.variableId);
        }
    }
    getValue() {
        if (this.type === Struct.Scene.Operation.Target.Type.NUMBER) {
            return this.value;
        }
        if (this.type === Struct.Scene.Operation.Target.Type.RANDOM) {
            return Math.round(Math.random() * (this.end - this.begin + 1) + this.begin);
        }
        if (this.type === Struct.Scene.Operation.Target.Type.VARIABLE) {
            return game_1.default.variable.find(this.variableId).value;
        }
    }
    toString() {
        let string = '';
        if (this.type === Struct.Scene.Operation.Target.Type.NUMBER) {
            string = `数值[${this.value}]`;
        }
        if (this.type === Struct.Scene.Operation.Target.Type.RANDOM) {
            string = `随机[${this.begin}, ${this.end}]`;
        }
        if (this.type === Struct.Scene.Operation.Target.Type.VARIABLE) {
            string = `变量[${this.variableId}]`;
        }
        return string;
    }
}
exports.Target = Target;



