import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';

import * as Model from '../model/';

import * as Layer from '../interaction/layer';

export default class Interactor {
    static triggers = [];
    static configures = [];
    /**
     * 配合ui的交互层
     */
    static layer = new Layer.List();

    static init() {
    }

    static reset() {
        this.layer.reset();
    }

    /**
     *
     * @param {Model.Interaction} interaction
     */
    static configure(interaction) {
        // console.log('Interatcor: configure ', interaction);
        if (_.has(this.configures, interaction.id)) {
            this.configures[interaction.id](interaction);
        }
    }

    /**
     *
     * @param {number} id
     * @param {function} configure
     */
    static bindConfigure(id, configure) {
        this.configures[id] = configure;
    }

    /**
     *
     * @param {Model.Interaction} interaction
     */
    static trigger(interaction) {
        // console.log('Interatcor: trigger ', interaction);
        if (_.has(this.triggers, interaction.id)) {
            this.triggers[interaction.id](interaction);
        }
    }

    /**
     *
     * @param {number} id
     * @param {function} trigger
     */
    static bindTrigger(id, trigger) {
        this.triggers[id] = trigger;
    }

    /**
     *
     * @param {InteractionTarget} target
     */
    static addTarget(target) {
        // console.log('bind event', target, this.layer.items.length);
        this.layer.add(target);
    }

    /**
     *
     * @param {InteractionTarget} target
     */
    static removeTarget(target) {
        this.layer.remove(target);
    }

    static popup() {
        this.layer.popup();
    }

    static popdown() {
        this.layer.popdown();
    }
}



