// @ts-check
import * as _ from 'lodash';

import {StateTransition, Transition} from '../utils/phaser-state-transition/';

import Modeler from './modeler';
import * as Model from '../model';

import Interactor from './interactor';
import Animator from './animator';

import * as Scene from '../render/scene';
import * as UI from '../render/ui';


export default class Renderer {

    static Scene = {
        INIT: 'Scene.Init',
        LOADING: 'Scene.Loading',
        TITLE: 'Scene.Title',
        NORMAL: 'Scene.Normal',
    };

    static ui = new UI.List();

    static current = '';

    /**
     * @type {Phaser.Game}
     */
    static game;

    /**
     * @type {StateTransition}
     */
    static stateTransition;

    /**
     *
     * @param {Phaser.Game} game
     */
    static init(game) {

        this.game = game;
        this.stateTransition = new StateTransition(game);

        this.addStates();
        this.bindInteractions();
    }

    static addStates() {
        this.game.state.add(this.Scene.INIT, Scene.Init);
        this.game.state.add(this.Scene.LOADING, Scene.Loading);
        this.game.state.add(this.Scene.TITLE, Scene.Sketch);
        this.game.state.add(this.Scene.NORMAL, Scene.Normal);
    }

    static bindInteractions() {
        Interactor.bindTrigger(
            Model.Interaction.Id.POPUP_SETTING_MENU,
            this.popupUI.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.GO_BACK,
            this.popdownUI.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.POPUP_LOAD_GAME,
            this.popupUI.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.POPUP_SAVE_GAME,
            this.popupUI.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.POPUP_QUICK_MENU,
            this.popupUI.bind(this)
        );
        Interactor.bindTrigger(
            Model.Interaction.Id.POPUP_PLAYBACK,
            this.popupUI.bind(this)
        );
    }

    static reset() {
        this.ui.reset();
        // Interactor.reset();
    }

    static findState(scene) {
        let stateKey = scene;
        let state;

        if (_.has(this.game.state.states, stateKey)) {
            state = this.game.state.states[stateKey];
            // state.onRenderFinish.removeAll();
            // state.onRenderFinish.addOnce(renderFinish);
        } else {
            throw new Error('Renderer: can not find ' + scene);
        }
        return state;
    }

    static bindRenderFinish(scene, renderFinish) {
        /**
         * @type {Scene.Base}
         */
        let state = this.findState(scene);

        state.onRenderFinish.removeAll();
        state.onRenderFinish.addOnce(renderFinish);
    }

    static renderInit(renderFinish) {
        this.bindRenderFinish(this.Scene.INIT, renderFinish);
        this.render(this.Scene.INIT);
    }

    /**
     *
     * @param {function} renderFinish
     * @param {*[]} loadinfos
     */
    static renderLoading(renderFinish, loadinfos, fileComplate) {

        let current = this.findState(this.game.state.current);

        let capturedScreen = current.captureScreen();

        this.bindRenderFinish(this.Scene.LOADING, renderFinish);

        /**
         * @type {Scene.Loading}
         */
        let state = this.findState(this.Scene.LOADING);

        state.onFileComplete.removeAll();
        state.onFileComplete.add(fileComplate);

        this.render(this.Scene.LOADING, loadinfos, capturedScreen);
    }

    /**
     *
     * @param {Model.Scene.Item.Base} model
     */
    static renderTitle(model) {

        this.render(this.Scene.TITLE, model);
    }

    /**
     *
     * @param {Model.Scene.Item.Normal} model
     * @param {boolean} [clearWorld]
     * @param {boolean} [clearCache]
     */
    static renderNormal(model, clearWorld, clearCache) {

        let stateKey = this.Scene.NORMAL;

        clearWorld = clearWorld || false;
        clearCache = clearCache || false;

        if (model.hasTransition()) {
            let transitionData = this.formatTransition(model.transition);
            this.stateTransition.start(stateKey, transitionData, clearWorld, clearCache, model);
        } else {
            this.render(this.Scene.NORMAL, model);
        }
    }

    static formatTransition(transition) {
        let result = null;
        switch(transition.type) {
            case Model.Scene.Item.Normal.TransitionType.CLOCK_CLOCKWISE:
                result = {
                    type: 'clock',
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.COVER_LEFT:
                result = {
                    type: 'cover',
                    direction: Transition.Cover.DIRECTION.LEFT,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.COVER_RIGHT:
                result = {
                    type: 'cover',
                    direction: Transition.Cover.DIRECTION.RIGHT,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.FADE_SMOOTHLY:
                result = {
                    type: 'fade',
                    isBlack: Transition.Fade.TYPE.SMOOTHLY,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.FADE_BLACK:
                result = {
                    type: 'fade',
                    isBlack: Transition.Fade.TYPE.BLACK,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.DISSOLVE:
                result = {
                    type: 'dissolve',
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.LINE_VERTICAL:
                result = {
                    type: 'line',
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.PUSH_TOP:
                result = {
                    type: 'push',
                    direction: Transition.Push.DIRECTION.TOP,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.PUSH_LEFT:
                result = {
                    type: 'push',
                    direction: Transition.Push.DIRECTION.LEFT,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.SHAPE_CIRCLE:
                result = {
                    type: 'shape',
                    shape: Transition.Shape.SHAPE.CIRCLE,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.SHAPE_DIAMOND:
                result = {
                    type: 'shape',
                    shape: Transition.Shape.SHAPE.DIAMOND,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.UNCOVER_LEFT:
                result = {
                    type: 'uncover',
                    direction: Transition.Uncover.DIRECTION.LEFT,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.UNCOVER_RIGHT:
                result = {
                    type: 'uncover',
                    direction: Transition.Uncover.DIRECTION.RIGHT,
                };
                break;
            case Model.Scene.Item.Normal.TransitionType.WIPE_LEFT:
                result = {
                    type: 'wipe',
                    direction: Transition.Wipe.DIRECTION.LEFT,
                };
                break;

        }
        return result;
    }

    /**
     *
     * @param {string} scene
     * @param {*[]} args
     */
    static render(scene, ...args) {
        this.current = scene;

        let stateKey = scene;

        var clearWorld = false;
        var clearCache = false;
        this.game.state.start(stateKey, clearWorld, clearCache, ...args);
    }

    static interactionIdtoTemplateType(id) {
        let type;
        if (id === Model.Interaction.Id.POPUP_SETTING_MENU) {
            type = Model.Template.Item.Type.SETTING;
        } else if (id === Model.Interaction.Id.POPUP_LOAD_GAME) {
            type = Model.Template.Item.Type.SAVED;
        } else if (id === Model.Interaction.Id.POPUP_SAVE_GAME) {
            type = Model.Template.Item.Type.SAVED;
        } else if (id === Model.Interaction.Id.POPUP_QUICK_MENU) {
            type = Model.Template.Item.Type.MENU;
        } else if (id === Model.Interaction.Id.POPUP_PLAYBACK) {
            type = Model.Template.Item.Type.PLAYBACK;
        }
        // 快捷测试使用
        // type = Model.Template.Item.Type.PLAYBACK;
        return type;
    }

    /**
     *
     * @param {Model.Interaction} interaction
     */
    static popupUI(interaction) {
        if (!this.ui.existUI()) {
            // 无UI -> 有UI, 游戏暂停
            Animator.pause();
            // this.game.tweens.pauseAll();
        }

        let id = interaction.id;
        /**
         * @type {Scene.Base}
         */
        let current = this.findState(this.game.state.current);

        let type = this.interactionIdtoTemplateType(id);
        let scene = Modeler.theme.template.findByType(type).getDefaultScene();

        /**
         * 是元素的一种，动态获取的
         * @type {*}
         */
        let element;
        if (id === Model.Interaction.Id.POPUP_LOAD_GAME) {
            element = scene.element.findByType(Model.Element.Item.Type.SAVED);
            console.assert(element, 'Renderer : 加载游戏时不能获取 Saved 元素');
            element.setIsSaving(false);
        } else if (id === Model.Interaction.Id.POPUP_SAVE_GAME) {
            element = scene.element.findByType(Model.Element.Item.Type.SAVED);
            console.assert(element, 'Renderer : 保存游戏时不能获取 Saved 元素');
            element.setIsSaving(true);
        } else if (id === Model.Interaction.Id.POPUP_PLAYBACK) {
            element = scene.element.findByType(Model.Element.Item.Type.PLAYBACK);
            console.assert(element, 'Renderer : 展示剧情回放时不能获取 PlayBack 元素');
            element.props.set('playback', Modeler.story.playback.get());
        }

        this.ui.popup(this.game, current.baseGroup, scene);
    }

    static popdownUI(isAll) {
        if (isAll) {
            this.ui.popdownAll();
        } else {
            this.ui.popdown();
        }
        if (!this.ui.existUI()) {
            // 有UI -> 无UI, 游戏恢复
            Animator.resume();
            // this.game.tweens.resumeAll();
        }
    }
}



