// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import Base from './base';

export default class Shake extends Base {
    static LEVEL = [5, 10, 15, 20, 25];
    constructor(game, data) {
        super(game);

        // 这里的数据转换应该移到model中去
        this.duration = _.get(data, 'duration', 1);
        this.level = _.get(data, 'level', 1);
    }

    run() {
        this.fixPivot();

        /**
         * @type {Phaser.Tween}
         */
        this.tween = this.game.add.tween(this.game.world);
        var angle = Shake.LEVEL[this.level - 1] / 2;
        var times = Math.floor(this.duration * 3);//规定每秒3次

        var cycle = 1 / 3 * 1000;
        var quarterCircle = cycle / 4;

        this.tween.to(
            { angle: -angle },
            quarterCircle,
            Phaser.Easing.Linear.None,
            false
        ).to(
            { angle: angle },
            quarterCircle * 2,
            Phaser.Easing.Linear.None,
            false
            ).to(
            { angle: 0 },
            quarterCircle,
            Phaser.Easing.Linear.None,
            false
            );
        if (times - 1 > 0) {
            this.tween.repeatAll(times - 1);
        }

        this.tween.start();

        this.tween.onComplete.addOnce(this.complete, this);

        return this.tween;
    }

    fixSpeed(isSpeedUp) {
        if (this.tween) {
            this.fixTweenspeed(this.tween, isSpeedUp);
        }
    }

    complete() {
        this.resumePivot();
        if (this.tween) {
            this.game.tweens.remove(this.tween);
        }
        super.complete();
    }

    pause() {
        if (this.tween) {
            this.tween.pause();
        }
        this.resumePivot();
        this.currentAngle = this.game.world.angle;
        this.game.world.angle = 0;
    }

    resume() {
        this.fixPivot();
        this.game.world.angle = this.currentAngle;
        if (this.tween) {
            this.tween.resume();
        }
    }

    fixPivot() {
        this.game.world.setBounds(-this.game.width, -this.game.height, this.game.width * 2, this.game.height * 2);
        this.game.camera.setPosition(-this.game.width / 2, -this.game.height / 2);
        this.game.world.pivot.setTo(this.game.width / 2, this.game.height / 2);
    }

    resumePivot() {
        this.game.world.setBounds(0, 0, this.game.width, this.game.height);
        this.game.camera.setPosition(0, 0);
        this.game.world.pivot.setTo(0, 0);
    }
}



