// @ts-check
import * as _ from 'lodash';
import { Phaser } from '../utils/load-phaser';
import ClickHandle from '../utils/click-handler';

import Interactor from './interactor';

import * as Model from '../model/';
import Modeler from '../core/modeler';
import Scene from '../render/scene';

import Sound from '../audio/sound';

const MODEL_KEY = 'model';

function calculateVolume(v1, v2) {
    return v1 * 0.01 * v2 * 0.01;
}

class Bgm {
    /**
     * @type {Phaser.Sound}
     */
    static sound;

    /**
     * @type {Model.Audio.Item.Bgm}
     */
    static model;

    static loopCount = 0;

    /**
     * @param {Phaser.Game} game
     * @param {Model.Audio.Item.Bgm} model
     * @param {number} volume
     */
    static play(game, model, volume) {
        this.model = model;

        if (model.isInherited) {
            return;
        }

        if (this.sound) {
            try {
                this.sound.destroy();
            } catch(e) {}
            this.sound = null;
        }

        /**
         * @type {Model.Material.Metainfo.Sound}
         */
        let metainfo = Modeler.material.find(model.materialId).getMetainfo();

        let loop = model.isLoop ? true : model.times > 1;
        let sound = game.add.audio(metainfo.loadinfo.key, calculateVolume(model.volume, volume), loop);

        sound.play();
        sound.onLoop.add(this.hasLooped, this);

        this.sound = sound;
    }

    static hasLooped() {
        if (this.model.isLoop) {
            return;
        }

        this.loopCount++;

        if(this.loopCount == this.model.times) {
            this.stop();
        }
    }

    static stop() {
        if (this.sound && this.sound.isPlaying) {
            try {
                this.sound.stop();
            } catch(e) {}
        }
    }

    static changeVolume(volume) {
        if (this.sound) {
            this.sound.volume = calculateVolume(this.model.volume, volume);
        }
    }
}

export default class AudioPlayer {
    /**
     * @type {Phaser.Game}
     */
    static game;

    static scene;

    static volumes = {
        bgm: 100,
        effect: 100,
        voice: 100,
    };

    static bgm = Bgm;
    /**
     * @type {Sound[]}
     */
    static sounds = [];

    /**
     * @type {Model.Audio.Item.Effect[]}
     */
    static models = [];
    static runIndex = -1;

    static clickHandler = new ClickHandle();

    static changeVolume(key, volume) {
        this.volumes[key] = volume;
        if (key === 'bgm') {
            this.bgm.changeVolume(volume);
        } else {
            for (var i = 0; i < this.sounds.length; i++) {
                let sound = this.sounds[i];
                /**
                 * @type {Model.Audio.Item.Effect}
                 */
                let model = _.get(sound, MODEL_KEY);
                if (key === 'voice' && model.isVoice) {
                    sound.volume = calculateVolume(model.volume, volume);
                } else if (key === 'effect' && !model.isVoice) {
                    sound.volume = calculateVolume(model.volume, volume);
                }
            }
        }
    }

    static reset() {
        this.models = [];
        this.runIndex = -1;
        this.clickHandler.reset();

        // 清除所有声音
        for (var i = 0; i < this.sounds.length; i++) {
            if (this.sounds[i]) {
                this.game.sound.remove(this.sounds[i]);
            }
        }
        this.sounds = [];
    }

    /**
     *
     * @param {Scene.Normal} scene
     */
    static setScene(scene) {
        this.scene = scene;
        this.game = scene.game;
        this.reset();
    }

    static playBgm(model) {
        this.bgm.play(this.game, model, this.volumes.bgm);
    }

    /**
     * @param {Model.Audio.Item.Effect} model
     */
    static add(model) {
        this.models.push(model);
    }

    /**
     * @param {Model.Audio.Item.Effect} model
     */
    static createSound(model) {
        /**
         * @type {Model.Material.Metainfo.Sound}
         */
        let metainfo = Modeler.material.find(model.materialId).getMetainfo();
        let sound = new Sound(
            this.game,
            metainfo.loadinfo.key,
            calculateVolume(model.volume, model.isVoice ? this.volumes.voice : this.volumes.effect),
            model
        );

        // @ts-ignore
        this.game.sound._sounds.push(sound);

        // _.set(sound, MODEL_KEY, model);
        sound.onStart.add(this.onSoundStartHandle, this);
        sound.onComplete.add(this.onSoundCompleteHandle, this);
        this.sounds.push(sound);
        return sound;
    }

    static getNext() {
        var model = null;
        let nextIndex = this.runIndex + 1;
        if (this.models.length > 0 && nextIndex < this.models.length) {
            model = this.models[nextIndex];
        }
        return model;
    }

    static start() {
        // 事件开始
        this.scene.event.start();
        Interactor.addTarget(this);
        this.runIndex = -1;
        let nextModel = this.getNext();

        if (nextModel) {
            let nextSound = this.createSound(nextModel);
            if (nextSound) {
                if (!Modeler.game.setting.isAutoPlay && nextModel.isTriggerClick()) {
                    this.bindClickHandle(nextSound.start, nextSound);
                } else {
                    nextSound.start();
                }
            }
        }
    }

    static update() {
        if (Modeler.game.setting.isAutoPlay && this.clickHandler.isEnable) {
            this.clickHandler.onClickHandle.call(this.clickHandler);
        }
    }

    static onSoundStartHandle(sound) {
        this.runIndex++;
        this.lastSound = sound;
        var nextModel = this.getNext();

        if (nextModel) {
            if (nextModel.isTriggerStart()) {
                let nextSound = this.createSound(nextModel);
                if (nextSound) {
                    nextSound.start();
                }
            }
        }
    }

    static onSoundCompleteHandle(sound) {

        let model = _.get(sound, MODEL_KEY);
        let lastModel = _.get(this.lastSound, MODEL_KEY);
        if (lastModel && model.id === lastModel.id) {

            var nextModel = this.getNext();

            if (nextModel) {
                let nextSound = this.createSound(nextModel);
                if (nextModel.isTriggerEnd()) {
                    nextSound.start();
                } else if (nextModel.isTriggerClick()) {
                    if (!Modeler.game.setting.isAutoPlay) {
                        this.bindClickHandle(nextSound.start, nextSound);
                    } else {
                        nextSound.start();
                    }
                }
            }

            if (this.hasCompleted()) {
                // _.set(sound, MODEL_KEY, null);
                this.onCompleteHandle();
            }
        } else {
            // _.set(sound, MODEL_KEY, null);
        }
    }

    static hasCompleted() {
        return this.runIndex + 1 == this.models.length;
    }

    static onCompleteHandle() {
        Interactor.removeTarget(this);
        // 事件结束
        this.scene.event.complate();
    }

    static bindClickHandle(listener, listenerContext) {
        this.clickHandler.bind(listener, listenerContext);
        this.scene.baseGroup.onChildInputDown.addOnce(this.clickHandler.onClickHandle, this.clickHandler);
    }

    static bindInteraction() {
        if (this.clickHandler.isEnable) {
            this.scene.baseGroup.onChildInputDown.addOnce(this.clickHandler.onClickHandle, this.clickHandler);
        }
    }
    static unbindInteraction() {
        if (this.clickHandler.isEnable) {
            this.scene.baseGroup.onChildInputDown.remove(this.clickHandler.onClickHandle, this.clickHandler);
        }
    }
}



