// @ts-check
import * as _ from 'lodash';

import { Phaser } from '../../utils/load-phaser';

import * as Model from '../../model';
import Base from './base';

export default class Animation extends Base {
    /**
     * @type {Phaser.Group}
     */
    parent;

    model;

    /**
     * @param {Phaser.Game} game
     * @param {Phaser.Group} parent
     * @param {Model.Element.Item.Base} model
     */
    constructor(game, parent, model, ...args) {
        super(game, parent, model, ...args);

        this.scale.x = model.w / this.getLocalBounds().width;
        this.scale.y = model.h / this.getLocalBounds().height;

        this.pivot.x = this.getLocalBounds().width / 2;
        this.pivot.y = this.getLocalBounds().height / 2;
        super.x = model.x + this.getLocalBounds().width / 2 * this.scale.x;
        super.y = model.y + this.getLocalBounds().height / 2 * this.scale.y;

        // console.log(`init.y = ${model.y}, format y = ${super.y}`);

        // this.scale.x = model.w / this.getLocalBounds().width;
        // this.scale.y = model.h / this.getLocalBounds().height;

    }

    get x() {
        return super.x - this.getLocalBounds().width / 2 * this.scale.x;
    }

    set x(x) {
        super.x = x + this.getLocalBounds().width / 2 * this.scale.x;
    }

    get y() {
        return super.y - this.getLocalBounds().height / 2 * this.scale.y;
    }

    set y(y) {
        super.y = y + this.getLocalBounds().height / 2 * this.scale.y;
        // console.log(`update.y = ${y}, format y = ${super.y}`);
    }

    get scaleWidth() {
        return this.width;
    }
    set scaleWidth(width) {
        let _x = this.x;
        this.scale.x = width / this.getLocalBounds().width;
        this.x = _x;
    }

    get scaleHeight() {
        return this.height;
    }
    set scaleHeight(height) {
        let _y = this.y;
        this.scale.y = height / this.getLocalBounds().height;
        this.y = _y;
    }

    get changeZ() {
        // 为了可以正常改变 z 轴，返回一个非改变的取值
        return 0;
    }
    set changeZ(z) {
        // console.log(`this.parent.children.length = ${this.parent.children.length}`);
        let target = null;
        if (z == 9999) {
            this.parent.bringToTop(this);
            return;
        }

        if (z == -9999) {
            this.parent.sendToBack(this);
            // 点击层
            this.parent.moveUp(this);
            // 背景层
            this.parent.moveUp(this);
            return;
        }

        if (z < 0) {
            do {
                this.parent.moveDown(this);
                z++;
            } while (z < 0);
            if (this.z == 0) {
                // 点击层
                this.parent.moveUp(this);
                // 背景层
                this.parent.moveUp(this);
            }
            return;
        }

        if (z > 0) {
            do {
                this.parent.moveUp(this);
                z--;
            } while (z > 0);
        }
    }
}



