// @ts-check

import { Phaser } from './utils/load-phaser';
import MSR from './utils/mobile-screen-rotation';
import * as _ from 'lodash';

import Director from './core/director';
import Url from './utils/url';

import * as Options from './model/options';
// @ts-ignore Phaser 全局配置
window['PhaserGlobal'] = {
    // disableWebAudio: true, // 使用 audio tag
};

export default class Engine {
    /**
     * @type {Options.Type}
     */
    static options;
    /**
     * @type {Phaser.Game}
     */
    static game;

    static director = Director;

    /**
     *
     * @param {Options.Type} customOptions
     */
    static init(customOptions) {
        this.initOption(customOptions);

        Phaser.Device.whenReady(this.whenReady, this);
    }

    /**
     *
     * @param {Options.Type} customOptions
     */
    static initOption(customOptions) {

        this.options = _.defaults(customOptions, Options.def);

        // 保证sectionId是个整数
        if (typeof this.options.sectionId === 'string') {
            this.options.sectionId = parseInt(this.options.sectionId);
            if (isNaN(this.options.sectionId)) {
                this.options.sectionId = Options.def.sectionId;
            }
        }

        Url.options.prefix = this.options.dataUrl;
        Url.options.bust = this.options.gameUpdateTime.toString();
        Url.options.systemPrefix = this.options.engineUrl;
    }

    static whenReady() {
        this.initGame();
        this.initEngine();
    }

    static initGame() {
        /**
         * @type {Phaser.IGameConfig}
         */
        let options = {
            width: this.options.width,
            height: this.options.height,
            renderer: Phaser.CANVAS,
            parent: this.options.container,
            state: null,
            transparent: false,
            antialias: true,
            physicsConfig: null,
            forceSetTimeOut: false,
        };

        // @ts-ignore d.ts 说明文件有误
        if (Phaser.Device.ie) {
            //该句可以解决IE下渲染左上角卡顿问题
            options.forceSetTimeOut = true;
        }

        this.game = new Phaser.Game(options);

        if (this.options.scale !== 1) {
            this.game.scale.scaleMode = Phaser.ScaleManager.USER_SCALE;
            this.game.scale.setUserScale(this.options.scale, this.options.scale);
        } else {
            if (!this.game.device.desktop) {
                // 加载 mobile-screen-rotation 手机屏幕自动旋转处理
                _.set(this.game, 'msr', this.game.plugins.add(MSR));
            } else {
                this.game.scale.scaleMode = Phaser.ScaleManager.USER_SCALE;


                let resize = function () {
                    this.game.scale.setUserScale(window.innerWidth / this.game.width, window.innerHeight / this.game.height);
                }.bind(this);

                resize();

                window.addEventListener('resize', resize);
            }
        }

    }

    static initEngine() {
        // game boot 后执行的环境处理
        var _this = this;
        var boot = function () {
            if (_this.game.isBooted) {
                _this.boot.call(_this);
            } else {
                setTimeout(boot, 50);
            }
        };
        boot();
    }

    static boot() {
        this.director.init(this.options, this.game);


        // 指定游戏加载的资源路径
        this.game.load.baseURL = _.trimEnd(this.options.dataUrl, '/') + '/';

        // 图片跨域解决
        this.game.load.crossOrigin = 'anonymous';
        //Url.systemFile = this.options.engineUrl;
        // @ts-ignore 修复 file 加载的判断出错的问题
        this.game.load.transformUrl = function (url, file) {

            if (!url) {
                return false;
            }

            if (url.match(/^(?:blob:|data:|http:\/\/|file:\/\/|https:\/\/|\/\/)/)) {
                return url;
            }
            else {
                return this.baseURL + file.path + url;
            }

        }

        // 开始执行游戏
        this.director.run();
    }
}

window['Monal'] = Engine;



