// @ts-check
import * as _ from 'lodash';


import { Phaser } from '../../../utils/load-phaser';

import Base from './base';

export default class Push extends Base {
    static DIRECTION = {
        TOP: "TOP",
        LEFT: "LEFT"
    }
    constructor(game, outView, inView, data) {
        super(game, outView, inView);
        this.direction = _.get(data, 'direction');
    }

    run() {
        this.game.world.add(this.outView);
        this.game.world.add(this.inView);
        var tweenOut = this.game.add.tween(this.outView);
        var tweenIn = this.game.add.tween(this.inView);
        if (this.direction == Push.DIRECTION.TOP) {
            tweenOut.to(
                { y: -this.game.height },
                1000,
                Phaser.Easing.Linear.None,
                true
            );
            tweenIn.from(
                { y: this.game.height },
                1000,
                Phaser.Easing.Linear.None,
                true
            );
        } else if (this.direction == Push.DIRECTION.LEFT) {
            tweenOut.to(
                { x: -this.game.width },
                1000,
                Phaser.Easing.Linear.None,
                true
            );
            tweenIn.from(
                { x: this.game.width },
                1000,
                Phaser.Easing.Linear.None,
                true
            );
        }

        tweenIn.onComplete.addOnce(this.complete, this);
    }

    complete() {
        this.game.world.remove(this.outView);
        this.game.world.remove(this.inView);
        super.complete();
    }
}



